<template>
  <div class="property">
    <!--  -->
    <div class="product-list-wrapper">
      <!--  -->
      <header class="table-header">
        <div class="table-header-left"></div>
        <div class="table-header-right">
          <label class="label">:</label>
          <el-select
            v-model="pageSize"
            clearable
            placeholder=""
            class="margin-26"
            @change="handlePageSize"
          >
            <el-option v-for="i in 10" :key="i" :label="i" :value="i"/>
          </el-select>
          <!-- <el-select v-model="nowSort" clearable placeholder="">
            <el-option v-for="item in sort" :key="item" :label="item" :value="item"/>
          </el-select>-->
          <el-button @click="$router.push({name:'add-property'})"></el-button>
          <el-button @click="$router.push({name:'styles'})"></el-button>
        </div>
      </header>
      <!--  -->
      <el-table
        ref="productTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="56"/>
        <el-table-column prop="id" label="" width/>
        <el-table-column prop="propertyName" label="" width/>
        <el-table-column prop="styleName" label="" width/>
        <el-table-column prop="propertySelect" label="">
          <template slot-scope="scope">{{scope.row.paramSelect == 2 ? '' : ''}}</template>
        </el-table-column>
        <el-table-column prop="propertyList" label="" width/>
        <el-table-column prop="propertyOrder" label="" width/>
        <el-table-column prop="name" label="" width>
          <template slot-scope="scope">
            <span @click="addProperty(scope.row.id)"></span>
            <span class="warning" @click="remove([scope.row.id])"></span>
          </template>
        </el-table-column>
      </el-table>
      <!-- + -->
      <footer>
        <div class="select-all-wrapper">
          <el-checkbox
            :indeterminate="isIndeterminate"
            v-model="checkAll"
            @change="handleCheckAllChange"
          ></el-checkbox>
          <el-select v-model="nowBatchOperation" clearable placeholder="" class="margin">
            <el-option v-for="item in batchOperation" :key="item" :value="item" :label="item"/>
          </el-select>
          <el-button @click="batchOperate"></el-button>
        </div>
        <div class="pagination-wrapper">
          <pagination
            :totalPage="totalPage"
            :dataCount="dataCount"
            :pageSize="pageSize"
            :currentPage="currentPage"
            @pageChange="pageChange"
          />
        </div>
      </footer>
    </div>
  </div>
</template>

<script>
import mixin from "@/util/mixin.js";
import Pagination from "../common/pagination";

export default {
  mixins: [mixin],
  components: {
    Pagination
  },
  data() {
    return {};
  },
  created() {
    this.getTableData(1);
  },
  methods: {
    //
    getTableData(page) {
      this.$axios({
        method: "post",
        url: "api/merchantGoodsProperty/merchant_goods_property_list_page",
        data: {
          styleId: this.$route.params.styleId
        }
      })
        .then(res => {
          console.log(res);
          this.tableData = res.data.data.list;
          this.totalPage = res.data.data.totalPage;
          this.dataCount = res.data.data.totalCount;
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    addProperty(id) {
      this.$router.push({
        name: "add-property",
        query: {
          propertyId: id
        }
      });
    },
    // (+)
    remove(ids) {
      //id[]
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$axios({
          method: "post",
          url: "api/merchantGoodsProperty/delete_batch",
          data: ids
        })
          .then(() => {
            this.msg("");
            this.getTableData();
          })
          .catch(err => {
            // console.log(err);
            this.msg(err, "error");
            this.getTableData();
          });
      });
    },
    batchOperate(){
      let arr = [];
      this.tableSelection.forEach(item => {
        arr.push(item.id);
      });
      switch (this.nowBatchOperation) {
        case "":
          this.msg('', 'error');
          break;
        case "":
          this.remove(arr);
          break;
      }
    }
  }
};
</script>


<style lang="scss" scoped>
@import "../../assets/scss/mixin.scss";

.property {
  @include table();
}
</style>
