export default {
    data() {
        return {
            loading: false, //
            showSearch: false, //
            isIndeterminate: false, //
            checkAll: false, //
            goodsName: "", //
            userName: '', //
            productTypes: [], //
            typeId: "", //
            brandId: "", //
            brandList: [], //
            sort: [], //
            nowSort: '', //
            tableData: [],
            pageSize: 10, //
            totalPage: 0, //
            dataCount: 0, //
            currentPage: 1, //
            tableSelection: [], //
            batchOperation: [""], //
            nowBatchOperation: "", //
            // uploadUrl: 'api/merchant/upload_file',
        }
    },
    computed: {
        showSeachTxt() {
            return this.showSearch ? "" : "";
        },
    },
    methods: {
        //
        submitForm(url, form, extraData) {
            let totalForm = Object.assign(form, extraData);
            //
            this.$axios({
                    method: "post",
                    url: url,
                    // // type: "form",
                    data: totalForm,
                    // //qsdataform
                    // transformRequest: [
                    //     function (data) {
                    //         data = Qs.stringify(data);
                    //         return data;
                    //     }
                    // ],
                    // // headerformdata
                    // headers: {
                    //     "Content-Type": "application/x-www-form-urlencoded"
                    // }
                })
                // .then(res => {
                //     // console.log(res);
                // })
                .catch(err => {
                    // console.log(err);
                    this.msg(err, 'error');
                    return;
                });
        },
        //
        searchShow() {
            this.showSearch = !this.showSearch;
        },
        //table selector
        handleSelectionChange(val) {
            this.tableSelection = val;
            if (this.tableSelection.length == this.tableData.length) {
                this.checkAll = true;
                this.isIndeterminate = false;
            } else {
                this.checkAll = false;
                this.isIndeterminate =
                    this.tableSelection.length > 0 &&
                    this.tableSelection.length < this.tableData.length ?
                    true :
                    false;
            }
        },
        //
        handleCheckAllChange(val) {
            if (val) {
                this.$refs.productTable.toggleAllSelection();
            } else {
                this.$refs.productTable.clearSelection();
            }
            this.tableSelection = val ? this.tableData : [];
            this.isIndeterminate = false;
        },
        //
        pageChange(page) {
            // console.log('mixin'+ page)
            this.getTableData(page);
            this.currentPage = page;
        },
        //
        handlePageSize(size) {
            this.pageSize = size;
            this.currentPage = 1;
            this.getTableData(1);
        },
        //formdata*qs.stringify()formdata()
        uploadImg(file) {
            return new Promise((resolve, reject) => {
                let formData = new FormData(); //formdata
                formData.append('file', file); //formdata()\
                //
                this.$axios({
                    method: 'post',
                    url: 'api/merchant/upload_file',
                    data: formData,
                    type: "form",
                    headers: {
                        "Content-Type": "multipart/form-data" //* json
                    },
                }).then((res) => {
                    // console.log(res);
                    resolve({
                        imgUrl: res.data.data.imgUrl + res.data.data.image
                    })
                }).catch(err => {
                    // console.log(err);
                    reject(err);
                })
            })
        },
        // uploadFile(content){
        //     this.uploadImg(content.file).then((res)=>{
        //         console.log(res);
        //     })
        // },
        //
        handleImg(urls) {
            let picArr = [];
            if (urls) {
                if (urls.indexOf(",") !== -1) {
                    picArr = urls.split(",");
                } else {
                    picArr.push(urls);
                }
            }
            return picArr[0];
        },
        // //
        // batchOperate() {
        //     let arr = [];
        //     this.tableSelection.forEach(item => {
        //         arr.push(item.id);
        //     });
        //     switch (this.nowBatchOperation) {
        //         case "":
        //             this.msg("", "error");
        //             break;
        //         case "":
        //             this.remove(arr);
        //             break;
        //     }
        // },
        //msg
        msg(str, type) {
            this.$message({
                message: str || "",
                type: type || "success",
                showClose: true,
                duration: 2500
            });
        },
        //
        goBack() {
            setTimeout(() => {
                this.$router.back();
            }, 500);
        }

    },
}