<template>
  <div class="add-site">
    <!-- title -->
    <div class="view-title">
      <i class="view-title-before"/>
      <span class="title">{{titleTxt}}</span>
      <div class="refresh-btn">
        <el-button icon="el-icon-close" @click="$router.go(-1)"></el-button>
      </div>
    </div>
    <!--  -->
    <div class="main-wrapper">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="150px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="returnPname">
          <el-input v-model="ruleForm.returnPname" placeholder=""/>
        </el-form-item>
        <el-form-item label="" prop="returnName">
          <el-input v-model="ruleForm.returnName" placeholder=""/>
        </el-form-item>
        <el-form-item label="" prop="returnAddress">
          <el-input v-model="ruleForm.returnAddress" placeholder="" type="textarea"/>
        </el-form-item>
        <el-form-item label="" prop="returnPhone">
          <el-input v-model="ruleForm.returnPhone" placeholder=""/>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitRuleForm('ruleForm')">{{titleTxt}}</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import mixin from "@/util/mixin.js";

export default {
  mixins: [mixin],
  data() {
    //
    var validatePhoneNumber = (rule, value, callback) => {
      let reg = /^1[3|4|5|7|8][0-9]{9}$/;
      if (!reg.test(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      isAdd: true,
      fileList: [],
      ruleForm: {
        id: "",
        merchantId: "",
        returnAddress: "",
        returnName: "",
        returnPhone: "",
        returnPname: ""
      },
      rules: {
        returnName: [
          { required: true, message: "", trigger: "blur" }
        ],
        returnPhone: [
          { required: true, message: "", trigger: "blur" },
          { validator: validatePhoneNumber, trigger: "blur" }
        ],
        returnPname: [
          { required: true, message: "", trigger: "blur" }
        ],
        returnAddress: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    if (this.$route.query.siteId) {
      this.isAdd = true;
      this.getFormData();
    } else {
      this.isAdd = false;
    }
  },
  computed: {
    titleTxt() {
      return this.isAdd ? "" : "";
    }
  },
  methods: {
    //data
    getFormData() {
      this.$axios({
        method: "post",
        url: "api/return_send/query_By_Id",
        data: {
          id: this.$route.query.siteId
        }
      })
        .then(res => {
          // console.log(res);
          this.ruleForm = res.data.data;
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },

    //
    submitRuleForm(formName) {
      this.$refs[formName].validate(valid => {
        //
        if (valid) {
          this.submitForm("api/return_send/add_or_update", this.ruleForm);
          this.$message({
            message: "",
            type: "success",
            showClose: true,
            duration: 2500
          });
          setTimeout(() => {
            this.$router.back();
          }, 3000);
        } else {
          this.msg("error submit!!", "error");
          return false;
        }
      });
    }
  }
};
</script>

