<template>
  <div class="add-ad">
    <!-- title -->
    <div class="view-title">
      <i class="view-title-before"/>
      <span class="title">{{titleTxt}}</span>
      <div class="refresh-btn">
        <el-button icon="el-icon-close" @click="$router.go(-1)"></el-button>
      </div>
    </div>
    <!--  -->
    <div class="main-wrapper">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="200px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="adName">
          <el-input v-model="ruleForm.adName" placeholder=""/>
        </el-form-item>
        <el-form-item label="" prop="adPosition">
          <el-select v-model="ruleForm.adPosition" placeholder="">
            <el-option
              v-for="item in typeList"
              :key="item.typeName"
              :label="item.typeName"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="online">
          <el-radio-group v-model="ruleForm.online">
            <el-radio :label="'1'"></el-radio>
            <el-radio :label="'0'"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="typeIcon">
          <img :src="ruleForm.adPicture" alt class="form-img" v-if="ruleForm.adPicture">
          <el-upload
            class="upload-demo"
            action="url"
            :http-request="uploadFile"
            accept="image/jpeg, image/png"
          >
            <el-button type="primary"></el-button>
            <div slot="tip" class="el-upload__tip">jpg/png</div>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="adLink">
          <el-autocomplete
            v-model="ruleForm.adLink"
            :fetch-suggestions="adLinkList"
            @select="handleAutocomplete"
            placeholder=""
          />
        </el-form-item>
        <el-form-item label="" prop="remark">
          <el-input v-model="ruleForm.remark" type="textarea"/>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitRuleForm('ruleForm')">{{titleTxt}}</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import mixin from "@/util/mixin.js";

export default {
  mixins: [mixin],
  data() {
    return {
      isAdd: true,
      typeList: [
        { id: "1", typeName: "APP" },
        { id: "2", typeName: "" }
      ],
      inputVisible: false,
      inputValue: "",
      fileList: [],
      adLinkId: "",
      ruleForm: {
        adLink: "",
        adName: "",
        adPicture: "",
        adPosition: "",
        id: "",
        online: "",
        remark: "",
        status: ""
      },
      rules: {
        adName: [
          { required: true, message: "", trigger: "blur" }
        ],
        adPosition: [
          { required: true, message: "", trigger: "change" }
        ],
        online: [
          { required: true, message: "", trigger: "change" }
        ],
        adLink: [
          { required: true, message: "", trigger: "change" }
        ],
        remark: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  created() {
    if (this.$route.query.adId) {
      this.isAdd = false;
      this.getFormData();
    } else {
      this.isAdd = true;
    }
  },
  computed: {
    titleTxt() {
      return this.isAdd ? "" : "";
    }
  },
  methods: {
    //data
    getFormData() {
      this.$axios({
        method: "post",
        url: "api/merchant_ad/get_single_merchant_ad",
        data: {
          id: this.$route.query.adId
        }
      })
        .then(res => {
          //   console.log(res);
          this.ruleForm = res.data.data;
          this.adLinkId2Name(res.data.data);
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    //  logo
    uploadFile(content) {
      this.uploadImg(content.file).then(res => {
        // console.log(res);
        this.ruleForm.adPicture = res.imgUrl;
      });
    },
    //(id)
    adLinkId2Name(data) {
      this.$axios({
        method: "post",
        url: "api/merchantGoods/merchant_goods_by_id",
        data: {
          id: data.adLink
        }
      })
        .then(res => {
          // console.log(res);
          this.ruleForm.adLink = res.data.data.goodsName;
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    adLinkList(queryStr, cb) {
      this.$axios({
        method: "post",
        url: "api/merchantGoods/merchant_goods_list_page",
        data: {
          currentPage: 1,
          pageSize: 100,
          goodsName: queryStr
        }
      }).then(res => {
        console.log(res);
        let arr = res.data.data.list;
        arr.forEach((val, index, arr) => {
          val.value = val.goodsName;
        });
        cb(arr);
      });
    },
    //
    handleAutocomplete($event) {
      // console.log($event);
      this.ruleForm.adLink = $event.goodsName;
      this.adLinkId = $event.id.toString();
    },

    //
    submitRuleForm(formName) {
      this.$refs[formName].validate(valid => {
        //
        if (valid) {
          if (this.isAdd) {
            //
            this.submitForm("api/merchant_ad/add_merchant_ad", this.ruleForm, {
              adLink: this.adLinkId
            });
          } else {
            this.submitForm(
              "api/merchant_ad/change_merchant_ad",
              this.ruleForm,
              { adLink: this.adLinkId }
            );
          }
          this.msg("");
          setTimeout(() => {
            this.$router.back();
          }, 50);
        } else {
          this.msg("error submit!!", "error");
          return false;
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/mixin.scss";

.add-ad {
  @include title();

  .main-wrapper {
    padding: 37px 45px 0 45px;
    display: flex;
    justify-content: center;
    .el-form {
      .el-form-item {
        img {
          width: 200px;
        }
      }
    }
  }
}
</style>
