<template>
  <div class="pay-setting">
    <!--  -->
    <div class="main-wrapper">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="200px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name" placeholder=""/>
        </el-form-item>
        <el-form-item label="" prop="detail">
          <el-input v-model="ruleForm.detail" type="textarea"/>
        </el-form-item>
        <el-form-item label="AppID" prop="appid">
          <el-input v-model="ruleForm.appid" placeholder="AppID"/>
        </el-form-item>
        <el-form-item label="AppSecret" prop="appsecret">
          <el-input v-model="ruleForm.appsecret" placeholder="AppSecret"/>
        </el-form-item>
        <el-form-item label="key" prop="appkey">
          <el-input v-model="ruleForm.appkey" placeholder="key"/>
        </el-form-item>
        <el-form-item label="" prop="mchId">
          <el-input v-model="ruleForm.mchId" placeholder=""/>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitRuleForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import mixin from "@/util/mixin.js";

export default {
  mixins: [mixin],
  data() {
    return {
      ruleForm: {
        appid: "",
        appkey: "",
        appsecret: "",
        detail: "",
        id: "",
        mchId: "",
        merchantId: "",
        name: "",
        remark1: "",
        remark2: "",
        remark3: ""
      },
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        appid: [{ required: true, message: "AppID", trigger: "blur" }],
        appsecret: [
          { required: true, message: "AppSecret", trigger: "blur" }
        ],
        appkey: [
          { required: true, message: "key", trigger: "blur" }
        ],
        mchId: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.getFormData();
  },
  computed: {},
  methods: {
    //data
    getFormData() {
      this.$axios({
        method: "post",
        url: "api/merchant_pay_mode/query"
      })
        .then(res => {
          // console.log(res);
          this.ruleForm = res.data.data;
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    //
    submitRuleForm(formName) {
      this.$refs[formName].validate(valid => {
        //
        if (valid) {
          this.submitForm("api/merchant_pay_mode/update", this.ruleForm);
          this.msg("");
          this.$nextTick(() => {
            this.getFormData();
          });
        } else {
          this.msg("error submit!!", "error");
          return false;
        }
      });
    }
  }
};
</script>

