<template>
  <div class="recycle">
    <!--  -->
    <div class="search-box">
      <div class="search-top">
        <div class="search-box-left">
          <span>
            <i class="el-icon-search"/>
          </span>
        </div>
        <div class="search-box-right">
          <span @click="searchShow">
            <i :class="showSearch ? 'el-icon-arrow-up' : 'el-icon-arrow-down'"/>
            {{showSeachTxt}}
          </span>
          <el-button @change="getTableData(1)"></el-button>
        </div>
      </div>
      <div class="search-bottom" v-if="showSearch">
        <div class="search-bar">
          <el-input v-model="goodsName" placeholder="/" clearable @change="getTableData(1)"/>
        </div>
        <div class="search-bar">
          <el-select v-model="typeId" clearable placeholder="" @change="getTableData">
            <el-option
              v-for="item in productTypes"
              :key="item.typeName"
              :label="item.typeName"
              :value="item.id"
            />
          </el-select>
        </div>
        <div class="search-bar">
          <el-select v-model="brandId" clearable placeholder="" @change="getTableData">
            <el-option
              v-for="item in brandList"
              :key="item.name"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="product-list-wrapper">
      <!--  -->
      <header class="table-header">
        <div class="table-header-left"></div>
        <div class="table-header-right">
          <label class="label">:</label>
          <el-select
            v-model="pageSize"
            clearable
            placeholder=""
            class="margin-26"
            @change="handlePageSize"
          >
            <el-option v-for="i in 10" :key="i" :label="i" :value="i"/>
          </el-select>
          <!-- <el-select v-model="nowSort" clearable placeholder="">
            <el-option v-for="item in sort" :key="item" :label="item" :value="item"/>
          </el-select>-->
        </div>
      </header>
      <!--  -->
      <el-table
        ref="productTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="56"/>
        <el-table-column prop="id" label="" width/>
        <el-table-column prop="goodsImg" label="" width>
          <template slot-scope="scope">
            <img :src="scope.row.goodsImg" alt>
          </template>
        </el-table-column>
        <el-table-column prop="goodsName" label="" width/>
        <el-table-column prop="typeName" label="" width/>
        <el-table-column prop="goodsPrice" label="" width/>
        <el-table-column prop="goodsNo" label="" width/>
        <el-table-column label="" width>
          <template slot-scope="scope">
            <span @click="restore(scope.row.id)"></span>
            <span class="warning" @click="remove([scope.row.id])"></span>
          </template>
        </el-table-column>
      </el-table>
      <!-- + -->
      <footer>
        <div class="select-all-wrapper">
          <el-checkbox
            :indeterminate="isIndeterminate"
            v-model="checkAll"
            @change="handleCheckAllChange"
          ></el-checkbox>
          <el-select v-model="nowBatchOperation" clearable placeholder="" class="margin">
            <el-option v-for="item in batchOperation" :key="item" :value="item" :label="item"/>
          </el-select>
          <el-button @click="batchOperate"></el-button>
        </div>
        <div class="pagination-wrapper">
          <pagination
            :totalPage="totalPage"
            :dataCount="dataCount"
            :pageSize="pageSize"
            :currentPage="currentPage"
            @pageChange="pageChange"
          />
        </div>
      </footer>
    </div>
  </div>
</template>

<script>
import mixin from "@/util/mixin.js";
import Pagination from "../common/pagination";

export default {
  mixins: [mixin],
  components: {
    Pagination
  },
  created() {
    this.getTableData();
    //
    this.$axios("api/merchantGoodsType/query_goods_type_tree")
      .then(res => {
        // console.log(res);
        this.productTypes = res.data.data;
      })
      .catch(err => {
        // console.log(err);
        this.msg(err, "error");
      });
    //
    this.$axios("api/merchant_goods_brand/query_list")
      .then(res => {
        // console.log(res);
        this.brandList = res.data.data;
      })
      .catch(err => {
        this.msg(err, "error");
      });
    //
    this.batchOperation.push("");
  },
  methods: {
    //
    getTableData(page) {
      this.$axios({
        method: "post",
        url: "api/merchantGoods/merchant_goods_recycling",
        data: {
          currentPage: page || this.currentPage,
          pageSize: this.pageSize,
          goodsName: this.goodsName,
          typeId: this.typeId,
          brandId: this.brandId
        }
      })
        .then(res => {
          // console.log(res);
          this.tableData = res.data.data.list;
          this.totalPage = res.data.data.totalPage;
          this.dataCount = res.data.data.totalCount;
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    //()
    restore(id) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$axios({
          method: "post",
          url: "api/merchantGoods/merchant_goods_recycling_reduction",
          data: {
            id: id
          }
        })
          .then(res => {
            this.msg("");
            this.getTableData();
          })
          .catch(err => {
            this.msg(err, "error");
          });
      });
    },
    //()
    bartchRestore(ids) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$axios({
          method: "post",
          url: "api/merchantGoods/merchant_goods_batch_reduction",
          data: ids
        })
          .then(res => {
            this.msg("");
            this.getTableData();
          })
          .catch(err => {
            this.msg(err, "error");
          });
      });
    },
    // (+)
    remove(id) {
      //id[]
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$axios({
          method: "post",
          url: "api/merchantGoods/delete_batch_recycling",
          data: id
        })
          .then(() => {
            this.msg("");
            this.getTableData();
          })
          .catch(err => {
            // console.log(err);
            this.msg(err, "error");
            this.getTableData();
          });
      });
    },
    //
    batchOperate() {
      let arr = [];
      this.tableSelection.forEach(item => {
        arr.push(item.id);
      });
      switch (this.nowBatchOperation) {
        case "":
          this.msg('', 'error');
          break;
        case "":
          this.remove(arr);
          break;
        case "":
          this.bartchRestore(arr);
          break;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/scss/mixin.scss";

div.recycle {
  @include table();
}
</style>
