<script>
import mixin from "@/util/mixin.js";
import Pagination from "../common/pagination";

export default {
  mixins: [mixin], //vue 
  components: {
    Pagination
  },
  data() {
    return {
      userData: [],
      user: {},
      address: [],
      orderData: [],
      deliveryData: {}
    };
  },
  created() {
    this.getUserData();
    this.getTableData();
  },
  computed: {},
  methods: {
    //(3)
    getUserData() {
      this.$axios({
        method: "post",
        url: "api/user_detail/query/" + this.$route.query.userId
      })
        .then(res => {
          console.log(res);
          this.userData.push(res.data.data);
          this.user = res.data.data.user;
          this.address = res.data.data.addressList;
        })
        .catch(err => {
          console.log(err);
        });
    },
    //(1)
    getTableData() {
      this.$axios({
        method: "post",
        url: "api/merchant_order/query_for_page",
        data: {
          currentPage: this.currentPage,
          pageSize: this.pageSize,
          userId: this.$route.query.userId,
          merchantId: JSON.parse(localStorage.user).merchantId
        }
      })
        .then(res => {
          //   console.log(res);
          this.tableData = res.data.data.list;
          this.totalPage = res.data.data.totalPage;
          this.dataCount = res.data.data.totalCount;
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    payTypeTxt(type) {
      return "";
    },
    //
    orderStatus(status) {
      //0123 45 6 20
      switch (status) {
        case 0:
          return "";
          break;
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case 3:
          return "";
          break;
        case 4:
          return "";
          break;
        case 5:
          return "";
          break;
        case 6:
          return "";
          break;
        case 20:
          return "";
          break;
      }
    },
    //
    deliveryInfo(id) {
      console.log(id);
      this.dialogVisible = true;
      this.$axios({
        method: "post",
        url: "api/merchant_order/query_By_Id",
        data: {
          id: id,
          merchantId: JSON.parse(localStorage.user).merchantId
        }
      })
        .then(res => {
          console.log(res);
          this.deliveryData = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

