<template>
  <div class="in">
    <!--  -->
    <div class="search-box">
      <div class="search-top">
        <div class="search-box-left">
          <span>
            <i class="el-icon-search"/>
          </span>
        </div>
        <div class="search-box-right">
          <span @click="searchShow">
            <i :class="showSearch ? 'el-icon-arrow-up' : 'el-icon-arrow-down'"/>
            {{showSeachTxt}}
          </span>
          <el-button @change="getTableData(1)"></el-button>
        </div>
      </div>
      <div class="search-bottom" v-if="showSearch">
        <div class="search-bar">
          <el-input v-model="name" clearable placeholder=" / " @change="getTableData()"/>
        </div>
        <div class="search-bar">
          <el-select v-model="typeId" clearable placeholder="" @change="getTableData()">
            <el-option
              v-for="item in productTypes"
              :key="item.name"
              :label="item.name"
              :value="item.code"
            />
          </el-select>
        </div>
        <div class="search-bar">
          <el-date-picker
            v-model="date"
            type="daterange"
            value-format="yyyy-MM-dd HH:mm:ss"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            @change="handleDate"
          />
        </div>
      </div>
    </div>
    <!--  -->
    <div class="product-list-wrapper">
      <!--  -->
      <header class="table-header">
        <div class="table-header-left"></div>
        <div class="table-header-right">
          <label class="label">:</label>
          <el-select
            v-model="pageSize"
            clearable
            placeholder=""
            class="margin-26"
            @change="handlePageSize"
          >
            <el-option v-for="i in 10" :key="i" :label="i" :value="i"/>
          </el-select>
        </div>
      </header>
      <!--  -->
      <el-table
        ref="productTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column prop="id" label="" width="60"/>
        <el-table-column prop="goodsImg" label="" width>
          <template slot-scope="scope">
            <img :src="handleImg(scope.row.goodsImg)" alt>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="" width>
          <template slot-scope="scope">
            <p>{{scope.row.goodsName}}</p>
            <p>{{scope.row.brandName}}</p>
          </template>
        </el-table-column>
        <el-table-column prop="goodsNo" label="/" width>
          <template slot-scope="scope">
            <p>{{scope.row.goodsNo}}/{{scope.row.property}}</p>
          </template>
        </el-table-column>
        <el-table-column prop="orderCode" label="" width/>
        <el-table-column prop="stock" label="" width>
          <template slot-scope="scope">
            <p>: {{ scope.row.changeNumber }}</p>
            <p>: {{ scope.row.changeAfter }}</p>
          </template>
        </el-table-column>
        <el-table-column prop="operateTypeName" label="" width/>
        <el-table-column prop="createTime" label="" width="180"/>
      </el-table>
      <!-- + -->
      <footer>
        <div class="select-all-wrapper"></div>
        <div class="pagination-wrapper">
          <pagination
            :totalPage="totalPage"
            :dataCount="dataCount"
            :pageSize="pageSize"
            :currentPage="currentPage"
            @pageChange="pageChange"
          />
        </div>
      </footer>
    </div>
  </div>
</template>

<script>
import Qs from "qs";
import mixin from "@/util/mixin.js";
import Pagination from "../common/pagination";

export default {
  mixins: [mixin], //vue 
  components: {
    Pagination
  },
  data() {
    return {
      stockTypeList: "",
      date: "",
      name:'',
      beginTime: "",
      endTime: ""
    };
  },
  created() {
    this.getTableData(1);
    //
    this.getStockType(1); //1 0 
  },
  computed: {},
  methods: {
    // 
    getStockType(type) {
      this.$axios({
        method: "post",
        url: "api/merchant_goods_log/query_type",
        type: "form",
        data: { type: type },
        //qsdataform
        transformRequest: [
          function(data) {
            data = Qs.stringify(data);
            return data;
          }
        ],
        // headerformdata
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      })
        .then(res => {
          // console.log(res);
          this.productTypes = res.data.data;
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    //
    getTableData(page) {
      this.$axios({
        method: "post",
        url: "api/merchant_goods_log/query_for_page",
        data: {
          currentPage: page || this.currentPage,
          pageSize: this.pageSize,
          search: this.name,
          operateType: this.typeId,
          beginTime: this.beginTime,
          endTime: this.endTime,
          orderCode: "",
          type: 1 //1 0 
        }
      })
        .then(res => {
          // console.log(res);
          this.tableData = res.data.data.list;
          this.totalPage = res.data.data.totalPage;
          this.dataCount = res.data.data.totalCount;
        })
        .catch(err => {
          this.mag(err, "error");
        });
    },
    //
    handleDate($date) {
      if ($date) {
        this.beginTime = $date[0];
        this.endTime = $date[1];
      } else {
        this.beginTime = "";
        this.endTime = "";
      }
      this.getTableData();
    }
  }
};
</script>


<style lang="scss" scoped>
@import "../../assets/scss/mixin.scss";

div.in {
  @include table();
  .search-box .search-bottom .search-bar {
    margin-right: 5px;
  }
  .el-table {
    table p {
      text-align: center;
    }
  }
}
</style>
