<template>
  <div class="styles">
    <!--  -->
    <div class="product-list-wrapper">
      <!--  -->
      <header class="table-header">
        <div class="table-header-left"></div>
        <div class="table-header-right">
          <label class="label">:</label>
          <el-select
            v-model="pageSize"
            clearable
            placeholder=""
            class="margin-26"
            @change="handlePageSize"
          >
            <el-option v-for="i in 10" :key="i" :label="i" :value="i"/>
          </el-select>
          <!-- <el-select v-model="nowSort" clearable placeholder="">
            <el-option v-for="item in sort" :key="item" :label="item" :value="item"/>
          </el-select>-->
          <el-button @click="editStyle()"></el-button>
        </div>
      </header>
      <!--  -->
      <el-table
        ref="productTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column label="" width>
          <template prop="id" slot-scope="scope">{{scope.row.id}}</template>
        </el-table-column>
        <el-table-column prop="styleName" label="" width></el-table-column>
        <el-table-column prop="propertyNum" label="" width></el-table-column>
        <el-table-column prop="paramNum" label=""></el-table-column>
        <el-table-column label="" width>
          <template slot-scope="scope">
            <span @click="showProperty(scope.row.id)"></span>
            <span @click="showParam(scope.row.id)"></span>
          </template>
        </el-table-column>
        <el-table-column label="" width>
          <template slot-scope="scope">
            <span @click="editStyle(scope.row)"></span>
            <span class="warning" @click="remove(scope.row.id)"></span>
          </template>
        </el-table-column>
      </el-table>
      <!-- + -->
      <footer>
        <div class="pagination-wrapper">
          <pagination
            :totalPage="totalPage"
            :dataCount="dataCount"
            :pageSize="pageSize"
            :currentPage="currentPage"
            @pageChange="pageChange"
          />
        </div>
      </footer>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" width="30%">
      <el-form
        :model="ruleForm"
        ref="ruleForm"
        label-width="100px"
        :rules="rules"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="styleName">
          <el-input v-model="ruleForm.styleName"/>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="submitRuleForm"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import mixin from "@/util/mixin.js";
import Pagination from "../common/pagination";

export default {
  mixins: [mixin],
  components: {
    Pagination
  },
  data() {
    return {
      tableData: [],
      dialogVisible: false,
      ruleForm: {
        styleName: ""
      },
      rules: {
        styleName: [
          { required: true, message: "", trigger: "blur" },
          { max: 20, message: "20", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.getTableData();
  },
  methods: {
    //
    getTableData(page) {
      this.$axios({
        method: "post",
        url: "api//merchantGoodsStyle/merchant_goods_style_list_page",
        data: {
          currentPage: page || this.currentPage,
          pageSize: this.pageSize
        }
      })
        .then(res => {
          console.log(res);
          this.tableData = res.data.data.list;
          this.totalPage = res.data.data.totalPage;
          this.dataCount = res.data.data.totalCount;
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    editStyle(row) {
      this.isAdd = row ? false : true;
      this.ruleForm = {};
      this.dialogVisible = true;
      if (!this.isAdd) {
        this.ruleForm.id = row.id;
        this.ruleForm.styleName = row.styleName;
      }
    },
    //
    submitRuleForm() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          if (this.isAdd) {
            //
            this.submitForm(
              "api/merchantGoodsStyle/merchant_goods_type_add",
              this.ruleForm
            );
          } else {
            this.submitForm(
              "api/merchantGoodsStyle/merchant_goods_type_update",
              this.ruleForm
            );
          }
          this.msg();
          this.dialogVisible = false;
          this.getTableData();
        }
      });
    },
    //
    remove(id) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$axios({
          method: "post",
          url: "api/merchantGoodsStyle/merchant_goods_type_delete",
          data: {
            id: id
          }
        })
          .then(() => {
            this.msg("");
            this.getTableData();
          })
          .catch(err => {
            // console.log(err);
            this.msg(err, "error");
            this.getTableData();
          });
      });
    },
    //
    showProperty(id) {
      this.$router.push({
        name: "property",
        params: {
          styleId: id
        }
      });
    },
    //
    showParam(id) {
      this.$router.push({
        name: "param",
        params: {
          styleId: id
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/scss/mixin.scss";

div.styles {
  @include table();

  footer {
    justify-content: flex-end !important;
  }
}
</style>
