<template>
  <div class="return-site">
    <!--  -->
    <div class="product-list-wrapper">
      <!--  -->
      <header class="table-header">
        <div class="table-header-left"></div>
        <div class="table-header-right">
          <label class="label">:</label>
          <el-select
            v-model="pageSize"
            clearable
            placeholder=""
            class="margin-26"
            @change="handlePageSize"
          >
            <el-option v-for="i in 10" :key="i" :label="i" :value="i"/>
          </el-select>
          <el-button @click="$router.push('add-site')"></el-button>
        </div>
      </header>
      <!--  -->
      <el-table
        id="out-table"
        ref="productTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="56"/>
        <el-table-column prop="id" label="" width/>
        <el-table-column prop="returnPname" label="" width/>
        <el-table-column prop="returnName" label="" width/>
        <el-table-column prop="returnAddress" label="" width/>
        <el-table-column prop="returnPhone" label="" width/>
        <el-table-column label="" width>
          <template slot-scope="scope">
            <span @click="$router.push({name:'add-site',query:{siteId:scope.row.id}})"></span>
            <span @click="remove([scope.row.id])"></span>
          </template>
        </el-table-column>
      </el-table>
      <!-- + -->
      <footer>
        <div class="select-all-wrapper">
          <el-checkbox
            :indeterminate="isIndeterminate"
            v-model="checkAll"
            @change="handleCheckAllChange"
          ></el-checkbox>
          <el-select v-model="nowBatchOperation" clearable placeholder="" class="margin">
            <el-option v-for="item in batchOperation" :key="item" :value="item" :label="item"/>
          </el-select>
          <el-button @click="batchOperate"></el-button>
        </div>
        <div class="pagination-wrapper">
          <pagination
            :totalPage="totalPage"
            :dataCount="dataCount"
            :pageSize="pageSize"
            :currentPage="currentPage"
            @pageChange="pageChange"
          />
        </div>
      </footer>
    </div>
  </div>
</template>

<script>
import mixin from "@/util/mixin.js";
import Pagination from "../common/pagination";

export default {
  mixins: [mixin], //vue 
  components: {
    Pagination
  },
  data() {
    return {
      status: "",
      deliveryData: {},
      dialogVisible: false,
      editGoodsNo: "",
      editProductInfo: ""
    };
  },
  created() {
    this.getTableData(1);
  },
  computed: {},
  methods: {
    //
    getTableData(page) {
      //
      this.$axios({
        method: "post",
        url: "api/return_send/query_for_page",
        data: {
          currentPage: page || this.currentPage,
          pageSize: this.pageSize,
          merchantId: JSON.parse(localStorage.user).merchantId
        }
      })
        .then(res => {
          // console.log(res);
          this.tableData = res.data.data.list;
          this.totalPage = res.data.data.totalPage;
          this.dataCount = res.data.data.totalCount;
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    // +
    remove(ids) {
      //id[]
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$axios({
          method: "post",
          url: "api/return_send/delete",
          data: { ids: ids }
        })
          .then(() => {
            this.msg("");
            this.getTableData();
          })
          .catch(err => {
            // console.log(err);
            this.msg(err, "error");
            this.getTableData();
          });
      });
    },
    //
    batchOperate() {
      let arr = [];
      this.tableSelection.forEach(item => {
        arr.push(item.id);
      });
      switch (this.nowBatchOperation) {
        case "":
          this.msg("", "error");
          break;
        case "":
          this.remove(arr);
          break;
        case "":
          this.remove(arr);
          break;
      }
    }
  }
};
</script>


<style lang="scss" scoped>
@import "../../assets/scss/mixin.scss";

div.return-site {
  @include table();

  //dialog
  .el-dialog {
    p {
      font-size: 16px;
      color: rgba(89, 87, 87, 1);
      line-height: 48px;
    }
  }
}
