<template>
  <div class="return">
    <!-- filter -->
    <div class="filter-group">
      <div class="search-filter" :class="status === '' ?  'active' : ''" @click="handleStatus(-1)">
        <p>
           (
          <span>{{orderCount.all}}</span>
          )
        </p>
      </div>
      <div class="search-filter" :class="status === 1 ?  'active' : ''" @click="handleStatus(1)">
        <p>
           (
          <span>{{orderCount.dcl}}</span>
          )
        </p>
      </div>
      <div class="search-filter" :class="status === 2 ?  'active' : ''" @click="handleStatus(2)">
        <p>
           (
          <span>{{orderCount.thz}}</span>
          )
        </p>
      </div>
      <div class="search-filter" :class="status === 6 ?  'active' : ''" @click="handleStatus(6)">
        <p>
           (
          <span>{{orderCount.ywc}}</span>
          )
        </p>
      </div>
      <div class="search-filter" :class="status === 3 ?  'active' : ''" @click="handleStatus(3)">
        <p>
           (
          <span>{{orderCount.yjj}}</span>
          )
        </p>
      </div>
    </div>

    <!--  -->
    <div class="search-box">
      <div class="search-top">
        <div class="search-box-left">
          <span>
            <i class="el-icon-search"/>
          </span>
        </div>
        <div class="search-box-right">
          <span @click="searchShow">
            <i :class="showSearch ? 'el-icon-arrow-up' : 'el-icon-arrow-down'"/>
            {{showSeachTxt}}
          </span>
          <el-button @change="getTableData(1)"></el-button>
        </div>
      </div>
      <div class="search-bottom" v-if="showSearch">
        <div class="search-bar">
          <el-input v-model="searchcode" placeholder="" clearable @change="getTableData(1)"/>
        </div>
        <div class="search-bar">
          <el-input v-model="receiver" placeholder="" clearable @change="getTableData(1)"/>
        </div>
        <div class="search-bar">
          <el-date-picker
            v-model="date"
            type="date"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd hh:mm:ss"
            @change="getTableData()"
          />
        </div>
      </div>
    </div>
    <!--  -->
    <div class="product-list-wrapper">
      <!--  -->
      <header class="table-header">
        <div class="table-header-left"></div>
        <div class="table-header-right">
          <label class="label">:</label>
          <el-select
            v-model="pageSize"
            clearable
            placeholder=""
            class="margin-26"
            @change="handlePageSize"
          >
            <el-option v-for="i in 10" :key="i" :label="i" :value="i"/>
          </el-select>
          <el-button @click="exportExcel"></el-button>
        </div>
      </header>
      <!--  -->
      <el-table
        id="out-table"
        ref="productTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="56"/>
        <el-table-column prop="code" label="" width/>
        <el-table-column prop="creatTime" label="" width="120"/>
        <el-table-column prop="mobilePhone" label="" width/>
        <el-table-column prop="applicationReturnMoney" label="" width/>
        <el-table-column prop="status" label="" width>
          <template slot-scope="scope">{{returnStatus(scope.row.status)}}</template>
        </el-table-column>
        <el-table-column label="" width>
          <template slot-scope="scope">
            <span @click="$router.push({name:'return-detail',query:{orderId:scope.row.id}})"></span>
            <span v-if="scope.row.status==0"></span>
          </template>
        </el-table-column>
      </el-table>
      <!-- + -->
      <footer>
        <div class="select-all-wrapper">
          <el-checkbox
            :indeterminate="isIndeterminate"
            v-model="checkAll"
            @change="handleCheckAllChange"
          ></el-checkbox>
          <el-select v-model="nowBatchOperation" clearable placeholder="" class="margin">
            <el-option v-for="item in batchOperation" :key="item" :value="item" :label="item"/>
          </el-select>
          <el-button @click="batchOperate"></el-button>
        </div>
        <div class="pagination-wrapper">
          <pagination
            :totalPage="totalPage"
            :dataCount="dataCount"
            :pageSize="pageSize"
            :currentPage="currentPage"
            @pageChange="pageChange"
          />
        </div>
      </footer>
    </div>
  </div>
</template>

<script>
import mixin from "@/util/mixin.js";
import Pagination from "../common/pagination";
import FileSaver from "file-saver";
import XLSX from "xlsx";

export default {
  mixins: [mixin], //vue 
  components: {
    Pagination
  },
  data() {
    return {
      status: "",
      statusList: [-1, 1, 2, 3, 6],
      searchcode: null,
      receiver: null,
      date: "",
      orderCount: {
        all: "",
        dcl: "",
        thz: "",
        ywc: "",
        yjj: ""
      },
      deliveryData: {},
      dialogVisible: false,
      editGoodsNo: "",
      editProductInfo: ""
    };
  },
  created() {
    this.getTableData(1);
    //
    this.$axios("api/merchantGoodsType/query_goods_type_tree")
      .then(res => {
        // console.log(res);
        this.productTypes = res.data.data;
      })
      .catch(err => {
        this.msg(err, "error");
      });
  },
  computed: {},
  methods: {
    handleStatus(status) {
      switch (status) {
        case -1:
          this.status = "";
          break;
        case 0:
          this.status = 0;
          break;
        default:
          this.status = status;
          break;
      }
      //   this.status = status === 0 ? 0 : status;
      this.getTableData();
    },
    //
    returnStatus(status) {
      //0123 45 6 20
      switch (status) {
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case 3:
          return "";
          break;
        case 4:
          return "";
          break;
        case 5:
          return "";
          break;
        default:
          return "";
      }
    },
    //
    getTableData(page) {
      //status
      let promiseAll = this.statusList.map(val =>
        this.$axios({
          method: "post",
          url: "api/merchant_return_goods/getCountByStatus",
          data: {
            merchantId: JSON.parse(localStorage.user).merchantId,
            status: val === -1 ? "" : val
          }
        })
      );
      let that = this;
      this.$axios.all(promiseAll).then(function(resArr) {
        //   console.log(resArr)
        resArr.forEach(function(res, i) {
          let statusName = "";
          switch (i) {
            case 0:
              statusName = "all";
              break;
            case 1:
              statusName = "dcl";
              break;
            case 2:
              statusName = "thz";
              break;
            case 3:
              statusName = "yjj";
              break;
            case 4:
              statusName = "ywc";
              break;
          }
          that.orderCount[statusName] = res.data.data;
        });
      });
      //
      this.$axios({
        method: "post",
        url: "api/merchant_return_goods/query_for_page",
        data: {
          currentPage: page || this.currentPage,
          pageSize: this.pageSize,
          merchantId: JSON.parse(localStorage.user).merchantId,
          contact: this.receiver,
          code: this.searchcode,
          status: this.status,
          creatTime: this.date
        }
      })
        .then(res => {
          // console.log(res);
          this.tableData = res.data.data.list;
          this.totalPage = res.data.data.totalPage;
          this.dataCount = res.data.data.totalCount;
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    // +
    remove(ids) {
      //id[]
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$axios({
          method: "post",
          url: "api/merchant_return_goods/delete",
          data: { ids: ids }
        })
          .then(() => {
            this.msg("");
            this.getTableData();
          })
          .catch(err => {
            // console.log(err);
            this.msg(err, "error");
            this.getTableData();
          });
      });
    },
    //
    batchOperate() {
      let arr = [];
      this.tableSelection.forEach(item => {
        arr.push(item.id);
      });
      switch (this.nowBatchOperation) {
        case "":
          this.msg("", "error");
          break;
        case "":
          this.remove(arr);
          break;
        case "":
          this.remove(arr);
          break;
      }
    },
    //
    exportExcel() {
      let time = new Date().getTime();
      /* generate workbook object from table */
      var wb = XLSX.utils.table_to_book(document.querySelector("#out-table"));
      /* get binary string as output */
      var wbout = XLSX.write(wb, {
        bookType: "xlsx",
        bookSST: true,
        type: "array"
      });
      try {
        FileSaver.saveAs(
          new Blob([wbout], { type: "application/octet-stream" }),
          time + ".xlsx"
        );
      } catch (e) {
        if (typeof console !== "undefined") console.log(e, wbout);
      }
      return wbout;
    }
  }
};
</script>


<style lang="scss" scoped>
@import "../../assets/scss/mixin.scss";

div.return {
  @include table();

  //dialog
  .el-dialog {
    p {
      font-size: 16px;
      color: rgba(89, 87, 87, 1);
      line-height: 48px;
    }
  }
}
</style>
