<template>
  <div class="account-list">
    <!--  -->
    <div class="search-box">
      <div class="search-top">
        <div class="search-box-left">
          <span>
            <i class="el-icon-search"/>
          </span>
        </div>
        <div class="search-box-right">
          <span @click="searchShow">
            <i :class="showSearch ? 'el-icon-arrow-up' : 'el-icon-arrow-down'"/>
            {{showSeachTxt}}
          </span>
          <el-button @change="getTableData(1)"></el-button>
        </div>
      </div>
      <div class="search-bottom" v-if="showSearch">
        <div class="search-bar">
          <el-input
            v-model="realName"
            clearable
            placeholder=" / "
            @change="getTableData()"
          />
        </div>
        <div class="search-bar">
          <el-input
            v-model="mobilePhone"
            clearable
            placeholder=" / "
            @change="getTableData()"
          />
        </div>
        <div class="search-bar">
          <el-date-picker
            v-model="date"
            type="daterange"
            value-format="yyyy-MM-dd HH:mm:ss"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            @change="handleDate"
          />
        </div>
      </div>
    </div>
    <!--  -->
    <div class="product-list-wrapper">
      <!--  -->
      <header class="table-header">
        <div class="table-header-left"></div>
        <div class="table-header-right">
          <label class="label">:</label>
          <el-select
            v-model="pageSize"
            clearable
            placeholder=""
            class="margin-26"
            @change="handlePageSize"
          >
            <el-option v-for="i in 10" :key="i" :label="i" :value="i"/>
          </el-select>
        </div>
      </header>
      <!--  -->
      <el-table
        ref="productTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column prop="id" label="ID" width/>
        <el-table-column prop="mobilePhone" label="" width/>
        <el-table-column prop="realName" label="" width/>
        <el-table-column prop="createTime" label="" width/>
        <el-table-column prop="userStatus" label="" width="160">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.userStatus"
              :inactive-value="0"
              :active-value="1"
              active-color="#5BC0BF"
              @change="handleShowStatus($event,scope.row)"
            />
          </template>
        </el-table-column>
        <el-table-column label="" width="160">
          <template slot-scope="scope">
            <span @click="$router.push({name:'account-detail',query:{userId:scope.row.id}})"></span>
          </template>
        </el-table-column>
      </el-table>
      <!-- + -->
      <footer>
        <div class="select-all-wrapper"/>
        <div class="pagination-wrapper">
          <pagination
            :totalPage="totalPage"
            :dataCount="dataCount"
            :pageSize="pageSize"
            :currentPage="currentPage"
            @pageChange="pageChange"
          />
        </div>
      </footer>
    </div>
  </div>
</template>

<script>
import Qs from "qs";
import mixin from "@/util/mixin.js";
import Pagination from "../common/pagination";

export default {
  mixins: [mixin],
  components: {
    Pagination
  },
  data() {
    return {
      date: "",
      realName: "",
      mobilePhone: "",
      beginTime: "",
      endTime: ""
    };
  },
  created() {
    this.getTableData();
  },
  methods: {
    //
    handleDate($date) {
      if ($date) {
        this.beginTime = $date[0];
        this.endTime = $date[1];
      } else {
        this.beginTime = "";
        this.endTime = "";
      }
      this.getTableData();
    },
    //
    getTableData(page) {
      this.$axios({
        method: "post",
        url: "api/user/get_user_list",
        type: "form",
        data: {
          currentPage: page || this.currentPage,
          pageSize: this.pageSize,
          mobilePhone: this.mobilePhone,
          realName: this.realName,
          beginTime: this.beginTime,
          endTime: this.endTime
        },
        //qsdataform
        transformRequest: [
          function(data) {
            data = Qs.stringify(data);
            return data;
          }
        ],
        // headerformdata
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      })
        .then(res => {
          // console.log(res);
          this.tableData = res.data.data.list;
          this.totalPage = res.data.data.totalPage;
          this.dataCount = res.data.data.totalCount;
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    //
    handleShowStatus($event, data) {
      let show = $event ? 1 : 0;
      this.$axios({
        method: "post",
        url: "api/user/change_user_status",
        data: {
          userId: data.id,
          userStatus: show
        },
        type: "form",
        //qsdataform
        transformRequest: [
          function(data) {
            data = Qs.stringify(data);
            return data;
          }
        ],
        // headerformdata
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      })
        .then(res => {
          //   console.log(res);
          this.msg("");
        })
        .catch(err => {
          this.msg(err, "error");
        });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/scss/mixin.scss";

div.account-list {
  @include table();
  .search-box .search-bottom .search-bar {
    margin-right: 5px;
  }
}
</style>
