<script>
import mixin from "@/util/mixin.js";
// import Tag from "./product-list-tag";
import Pagination from "../common/pagination";

export default {
  mixins: [mixin], //vue 
  components: {
    // Tag,
    Pagination
  },
  data() {
    return {
      status: 0,
      dialogVisible: false,
      editGoodsNo: "",
      goodsId: "",
      stockList: []
    };
  },
  created() {
    this.getTableData(1);
    //
    this.$axios("api/merchantGoodsType/query_goods_type_tree")
      .then(res => {
        // console.log(res);
        this.productTypes = res.data.data;
      })
      .catch(err => {
        this.msg(err, "error");
      });
    //
    this.$axios("api/merchant_goods_brand/query_list")
      .then(res => {
        this.brandList = res.data.data;
      })
      .catch(err => {
        this.msg(err, "error");
      });
  },
  computed: {},
  methods: {
    //url
    transImg(src) {
      if (src) {
        return src.split(",")[0];
      }
    },
    statusTxt(status) {
      return status == 1 ? "" : "";
    },
    //
    addProduct() {
      this.$router.push({ name: "add-product" });
    },
    //
    recInt(num) {
      return parseInt(num) == num ? num + ".00" : num;
    },
    handleStatus(status) {
      // console.log(status);
      if (status == -1) {
        this.status = "";
      } else {
        this.status = status;
      }
      this.getTableData();
    },
    //
    getTableData(page) {
      this.loading = true;
      this.$axios({
        method: "post",
        url: "api/merchantGoods/merchant_goods_list_page",
        data: {
          currentPage: page || this.currentPage,
          pageSize: this.pageSize,
          // status: this.status,
          goodsName: this.goodsName,
          typeId: this.typeId,
          brandId: this.brandId
        }
      })
        .then(res => {
          // console.log(res);
          this.tableData = res.data.data.list;
          this.totalPage = res.data.data.totalPage;
          this.dataCount = res.data.data.totalCount;
          this.loading = false;
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    //
    handleShowStatus($event, data) {
      let show = $event ? 1 : 0;
      if (show) {
        //
        this.$axios({
          method: "post",
          url: "api/merchantGoods/merchant_goods_put",
          data: {
            id: data.id
          }
        })
          .then(() => {
            this.getTableData();
          })
          .catch(err => {
            this.msg(err, "error");
          });
      } else {
        this.$axios({
          method: "post",
          url: "api/merchantGoods/merchant_goods_pull",
          data: {
            id: data.id
          }
        })
          .then(() => {
            this.getTableData();
          })
          .catch(err => {
            this.msg(err, "error");
          });
      }
    },

    //
    editProduct(id, no) {
      // 
      this.dialogVisible = true;
      this.loading = true;
      this.editGoodsNo = no;
      this.goodsId = id;
      //
      this.$axios({
        method: "post",
        url: "api/merchantGoods/merchant_goods_by_id",
        data: {
          id: id
        }
      })
        .then(res => {
          // console.log(res);
          this.adjust(res.data.data);
          this.loading = false;
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    //
    adjust(data) {
      this.stockList = [];
      this.stockList = data.merchantGoodsTypePropertyList;
      this.stockList.forEach(val => {
        val.goodsName = data.goodsName;
      });
    },
    //
    updateStock() {
      for (let i = 0, len = this.stockList.length; i < len; i++) {
        if (
          !this.stockList[i].goodsSalePrice ||
          !this.stockList[i].goodsStock ||
          !this.stockList[i].stockWarning
        ) {
          this.msg("", "error");
          return;
        }
      }
      //formgoodsName
      delete this.stockList[0].goodsName;
      this.$axios({
        method: "post",
        url: "api/merchantGoods/merchant_goods_update_sku",
        data: {
          id: this.goodsId,
          merchantGoodsTypePropertyList: this.stockList
        }
      })
        .then(res => {
          this.msg("");
          this.dialogVisible = false;
          this.getTableData();
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    //
    objectSpanMethod({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) {
        return {
          rowspan: 2,
          colspan: 1
        };
      } else {
        return {
          rowspan: 1,
          colspan: 1
        };
      }
    },
    // +
    remove(ids) {
      //id[]
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$axios({
          method: "post",
          url: "api/merchantGoods/delete_batch",
          data: ids
        })
          .then(() => {
            this.msg("");
            this.getTableData();
          })
          .catch(err => {
            // console.log(err);
            this.msg(err, "error");
            this.getTableData();
          });
      });
    },
    //
    batchOperate() {
      let arr = [];
      this.tableSelection.forEach(item => {
        arr.push(item.id);
      });
      switch (this.nowBatchOperation) {
        case "":
          this.msg("", "error");
          break;
        case "":
          this.remove(arr);
          break;
        case "":
          this.remove(arr);
          break;
      }
    }
  }
};
</script>


