<template>
  <div class="brands">
    <!--  -->
    <div class="search-box">
      <div class="search-top">
        <div class="search-box-left">
          <span>
            <i class="el-icon-search"/>
          </span>
        </div>
        <div class="search-box-right">
          <span @click="searchShow">
            <i :class="showSearch ? 'el-icon-arrow-up' : 'el-icon-arrow-down'"/>
            {{showSeachTxt}}
          </span>
          <el-button @click="getTableData(1)"></el-button>
        </div>
      </div>
      <div class="search-bottom" v-if="showSearch">
        <div class="search-bar">
          <el-input v-model="name" placeholder="/" clearable @change="getTableData(1)"/>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="product-list-wrapper">
      <!--  -->
      <header class="table-header">
        <div class="table-header-left"></div>
        <div class="table-header-right">
          <label class="label">:</label>
          <el-select
            v-model="pageSize"
            clearable
            placeholder=""
            class="margin-26"
            @change="handlePageSize"
          >
            <el-option v-for="i in 10" :key="i" :label="i" :value="i"/>
          </el-select>
          <!-- <el-select v-model="nowSort" clearable placeholder="">
            <el-option v-for="item in sort" :key="item" :label="item" :value="item"/>
          </el-select>-->
          <el-button @click="$router.push({name: 'add-brand'})"></el-button>
        </div>
      </header>
      <!--  -->
      <el-table
        ref="productTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="56"/>
        <el-table-column prop="id" label="" width/>
        <el-table-column prop="name" label="" width/>
        <el-table-column prop="firstChar" label="" width/>
        <el-table-column prop="sorting" label="" width/>
        <el-table-column prop="isShow" label="" width>
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.isShow"
              :inactive-value="0"
              :active-value="1"
              active-color="#5BC0BF"
              @change="handleShowStatus($event,scope.row)"
            />
          </template>
        </el-table-column>
        <el-table-column prop="goodsUnit" label="" width>
          <template slot-scope="scope">{{scope.row.goodsTotal}}</template>
        </el-table-column>
        <el-table-column label="" width="140">
          <template slot-scope="scope">
            <span @click="$router.push({name:'add-brand',query:{brandId:scope.row.id}})"></span>
            <span class="warning" @click="remove([scope.row.id])"></span>
          </template>
        </el-table-column>
      </el-table>
      <!-- + -->
      <footer>
        <div class="select-all-wrapper">
          <el-checkbox
            :indeterminate="isIndeterminate"
            v-model="checkAll"
            @change="handleCheckAllChange"
          ></el-checkbox>
          <el-select v-model="nowBatchOperation" clearable placeholder="" class="margin">
            <el-option v-for="item in batchOperation" :key="item" :value="item" :label="item"/>
          </el-select>
          <el-button @click="batchOperate"></el-button>
        </div>
        <div class="pagination-wrapper">
          <pagination
            :totalPage="totalPage"
            :dataCount="dataCount"
            :pageSize="pageSize"
            :currentPage="currentPage"
            @pageChange="pageChange"
          />
        </div>
      </footer>
    </div>
  </div>
</template>

<script>
import mixin from "@/util/mixin.js";
import Pagination from "../common/pagination";

export default {
  mixins: [mixin],
  components: {
    Pagination
  },
  data() {
    return {
      name: ""
    };
  },
  created() {
    this.getTableData();
  },
  methods: {
    //
    getTableData(page) {
      this.$axios({
        method: "post",
        url: "api/merchant_goods_brand/query_for_page",
        data: {
          currentPage: page || this.currentPage,
          pageSize: this.pageSize,
          name: this.name
        }
      })
        .then(res => {
          console.log(res);
          this.tableData = res.data.data.list;
          this.totalPage = res.data.data.totalPage;
          this.dataCount = res.data.data.totalCount;
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    handleShowStatus($event, data) {
      let show = $event ? 1 : 0;
      this.$axios({
        method: "post",
        url: "api/merchant_goods_brand/update",
        data: {
          id: data.id,
          name: data.name,
          logo: data.logo,
          isShow: show
        }
      })
        .then(res => {
          //   console.log(res);
          this.$message({
            message: "",
            type: "success",
            showClose: true,
            duration: 2500
          });
        })
        .catch(err => {
          console.log(err);
        });
    },
    // (+)
    remove(ids) {
      //id[]
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$axios({
          method: "post",
          url: "api/merchant_goods_brand/delete_batch",
          data: ids
        })
          .then(() => {
            this.msg("");
            this.getTableData();
          })
          .catch(err => {
            // console.log(err);
            this.msg(err, "error");
            this.getTableData();
          });
      });
    },
    batchOperate() {
      let arr = [];
      this.tableSelection.forEach(item => {
        arr.push(item.id);
      });
      switch (this.nowBatchOperation) {
        case "":
          this.msg("", "error");
          break;
        case "":
          this.remove(arr);
          break;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/scss/mixin.scss";

div.brands {
  @include table();
}
</style>
