<template>
  <div class="login-log">
    <!--  -->
    <div class="product-list-wrapper">
      <!--  -->
      <header class="table-header">
        <div class="table-header-left"></div>
        <div class="table-header-right">
          <label class="label">:</label>
          <el-select
            v-model="pageSize"
            clearable
            placeholder=""
            class="margin-26"
            @change="handlePageSize"
          >
            <el-option v-for="i in 10" :key="i" :label="i" :value="i"/>
          </el-select>
        </div>
      </header>
      <!--  -->
      <el-table
        ref="productTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column prop="operateTime" label="" width/>
        <el-table-column prop="ip" label="IP" width/>
        <el-table-column prop="region" label="" width>
          <template slot-scope="scope">{{region(scope.row.description)}}</template>
        </el-table-column>
        <el-table-column prop="pack" label="" width/>
      </el-table>
      <!-- + -->
      <footer>
        <div class="select-all-wrapper"></div>
        <div class="pagination-wrapper">
          <pagination
            :totalPage="totalPage"
            :dataCount="dataCount"
            :pageSize="pageSize"
            :currentPage="currentPage"
            @pageChange="pageChange"
          />
        </div>
      </footer>
    </div>
  </div>
</template>

<script>
//axiosqsrequest
import Qs from "qs";
import mixins from "@/util/mixin.js";
import Pagination from "../common/pagination.vue";

export default {
  mixins: [mixins],
  components: {
    Pagination
  },
  data() {
    return {
      // tableData: [],
      // pageSize: 13,
      // totalPage: Number,
      // dataCount: Number
    };
  },
  created() {
    this.getTableData(1);
  },
  methods: {
    getTableData(page) {
      this.$axios({
        method: "post",
        url: "api/merchant/get_merchant_login_info_list",
        data: {
          currentPage: page || this.currentPage,
          pageSize: this.pageSize,
          orderBy: "operate_time desc"
        },
        // type: "form",
        //qsdataform
        transformRequest: [
          function(data) {
            data = Qs.stringify(data);
            return data;
          }
        ],
        // headerformdata
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      })
        .then(res => {
          // console.log(res);
          this.tableData = res.data.data.list;
          this.totalPage = res.data.data.totalPage;
          this.dataCount = res.data.data.totalCount;
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    region(data) {
      let re = {};
      if (data.length > 2) {
        re = JSON.parse(data).data;
        return re.country + " - " + re.region + " - " + re.city;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/scss/mixin.scss";

.login-log {
  @include table();

}
</style>
