<template>
  <div class="order-list">
    <!-- filter -->
    <div class="filter-group">
      <div class="search-filter" :class="status === '' ?  'active' : ''" @click="handleStatus(-1)">
        <p>
           (
          <span>{{orderCount.allOrder}}</span>
          )
        </p>
      </div>
      <div class="search-filter" :class="status === 1 ?  'active' : ''" @click="handleStatus(1)">
        <p>
           (
          <span>{{orderCount.dfk}}</span>
          )
        </p>
      </div>
      <div class="search-filter" :class="status === 2 ?  'active' : ''" @click="handleStatus(2)">
        <p>
           (
          <span>{{orderCount.dfh}}</span>
          )
        </p>
      </div>
      <div class="search-filter" :class="status === 3 ?  'active' : ''" @click="handleStatus(3)">
        <p>
           (
          <span>{{orderCount.dsh}}</span>
          )
        </p>
      </div>
      <div class="search-filter" :class="status === 5 ?  'active' : ''" @click="handleStatus(5)">
        <p>
           (
          <span>{{orderCount.ywc}}</span>
          )
        </p>
      </div>
      <div class="search-filter" :class="status === 0 ?  'active' : ''" @click="handleStatus(0)">
        <p>
           (
          <span>{{orderCount.ygb}}</span>
          )
        </p>
      </div>
    </div>

    <!--  -->
    <div class="search-box">
      <div class="search-top">
        <div class="search-box-left">
          <span>
            <i class="el-icon-search"/>
          </span>
        </div>
        <div class="search-box-right">
          <span @click="searchShow">
            <i :class="showSearch ? 'el-icon-arrow-up' : 'el-icon-arrow-down'"/>
            {{showSeachTxt}}
          </span>
          <el-button @change="getTableData(1)"></el-button>
        </div>
      </div>
      <div class="search-bottom" v-if="showSearch">
        <div class="search-bar">
          <el-input v-model="searchcode" placeholder="" clearable @change="getTableData(1)"/>
        </div>
        <div class="search-bar">
          <el-input v-model="receiver" placeholder="/" clearable @change="getTableData(1)"/>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="product-list-wrapper">
      <!--  -->
      <header class="table-header">
        <div class="table-header-left"></div>
        <div class="table-header-right">
          <label class="label">:</label>
          <el-select
            v-model="pageSize"
            clearable
            placeholder=""
            class="margin-26"
            @change="handlePageSize"
          >
            <el-option v-for="i in 10" :key="i" :label="i" :value="i"/>
          </el-select>
          <el-button @click="exportExcel"></el-button>
        </div>
      </header>
      <!--  -->
      <el-table
        id="out-table"
        ref="productTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" prop="status" width="56" :selectable="selectable"/>
        <el-table-column prop="code" label="" width/>
        <el-table-column prop="creatTime" label="" width="120"/>
        <el-table-column prop="mobilePhone" label="" width/>
        <el-table-column prop="totalMoeny" label="" width/>
        <el-table-column prop="payType" label="" width>
          <template slot-scope="scope">{{payTypeTxt(scope.row.payType)}}</template>
        </el-table-column>
        <el-table-column prop="payType" label="" width>
          <template slot-scope="scope">{{payTypeTxt(scope.row.payType)}}</template>
        </el-table-column>
        <el-table-column prop="status" label="" width>
          <template slot-scope="scope">{{orderStatus(scope.row.status)}}</template>
        </el-table-column>
        <el-table-column label="" width>
          <template slot-scope="scope">
            <span
              @click="$router.push({name:'order-detail',query:{orderId:scope.row.id,mobilePhone:scope.row.mobilePhone}})"
            ></span>
            <span v-if="scope.row.status==2"></span>
            <span v-if="scope.row.status==0" @click="remove([scope.row.id])"></span>
            <span
              v-if="scope.row.status==5||scope.row.status==6"
              @click="deliveryInfo(scope.row.id)"
            ></span>
            <span v-if="scope.row.status==1" @click="close([scope.row.id])"></span>
          </template>
        </el-table-column>
      </el-table>
      <!-- + -->
      <footer>
        <div class="select-all-wrapper">
          <el-checkbox
            :indeterminate="isIndeterminate"
            v-model="checkAll"
            @change="handleCheckAllChange"
          ></el-checkbox>
          <el-select v-model="nowBatchOperation" clearable placeholder="" class="margin">
            <el-option v-for="item in batchOperation" :key="item" :value="item" :label="item"/>
          </el-select>
          <el-button @click="batchOperate"></el-button>
        </div>
        <div class="pagination-wrapper">
          <pagination
            :totalPage="totalPage"
            :dataCount="dataCount"
            :pageSize="pageSize"
            :currentPage="currentPage"
            @pageChange="pageChange"
          />
        </div>
      </footer>
    </div>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible">
      <p>{{deliveryData.sendCode}}</p>
      <p>{{deliveryData.sendCompany}}</p>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import mixin from "@/util/mixin.js";
import Pagination from "../common/pagination";
import FileSaver from "file-saver";
import XLSX from "xlsx";

export default {
  mixins: [mixin], //vue 
  components: {
    Pagination
  },
  data() {
    return {
      status: "",
      searchcode: null,
      receiver: null,
      orderCount: {},
      deliveryData: {},
      dialogVisible: false,
      editGoodsNo: "",
      editProductInfo: ""
    };
  },
  created() {
    this.getTableData(1);
    //
    this.batchOperation.push("");
  },
  computed: {},
  methods: {
    handleStatus(status) {
      switch (status) {
        case -1:
          this.status = "";
          break;
        case 0:
          this.status = 0;
          break;
        default:
          this.status = status;
          break;
      }
      //   this.status = status === 0 ? 0 : status;
      this.getTableData();
    },
    //
    payTypeTxt(type) {
      return "";
    },
    //
    orderStatus(status) {
      //0123 45 6 20
      switch (status) {
        case 0:
          return "";
          break;
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case 3:
          return "";
          break;
        case 4:
          return "";
          break;
        case 5:
          return "";
          break;
        case 6:
          return "";
          break;
        case 20:
          return "";
          break;
      }
    },
    //
    getTableData(page) {
      //status
      this.$axios({
        method: "post",
        url: "api/merchant_order/getOrderAllCount",
        data: {
          merchantId: JSON.parse(localStorage.user).merchantId
        }
      })
        .then(res => {
          // console.log(res);
          this.orderCount = res.data.data;
        })
        .catch(err => {
          // console.log(err);
          this.msg("" + err, "error");
        });
      //order-list data
      this.$axios({
        method: "post",
        url: "api/merchant_order/query_for_page",
        data: {
          currentPage: page || this.currentPage,
          pageSize: this.pageSize,
          merchantId: JSON.parse(localStorage.user).merchantId,
          phone: this.receiver || null,
          code: this.searchcode || null,
          status: this.status
        }
      })
        .then(res => {
          // console.log(res);
          this.tableData = res.data.data.list;
          this.totalPage = res.data.data.totalPage;
          this.dataCount = res.data.data.totalCount;
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    //
    selectable(row) {
      if (this.nowBatchOperation == "") {
        if (row.status == 0) {
          return true;
        }
      } else if (this.nowBatchOperation == "") {
        if (row.status == 1) {
          return true;
        }
      }
      return false;
    },
    //
    deliveryInfo(id) {
      // console.log(id);
      this.dialogVisible = true;
      this.$axios({
        method: "post",
        url: "api/merchant_order/query_By_Id",
        data: {
          id: id,
          merchantId: JSON.parse(localStorage.user).merchantId
        }
      })
        .then(res => {
          this.deliveryData = res.data.data;
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    //
    close(ids) {
      //id[]
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$axios({
          method: "post",
          url: "api/merchant_order/close",
          data: { ids }
        })
          .then(() => {
            this.msg("");
            this.getTableData();
          })
          .catch(err => {
            // console.log(err);
            this.msg(err, "error");
            this.getTableData();
          });
      });
    },

    // +
    remove(ids) {
      //id[]
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$axios({
          method: "post",
          url: "api/merchant_order/delete",
          data: { ids: ids }
        })
          .then(() => {
            this.msg("");
            this.getTableData();
          })
          .catch(err => {
            // console.log(err);
            this.msg(err, "error");
            this.getTableData();
          });
      });
    },
    //
    batchOperate() {
      let arr = [];
      this.tableSelection.forEach(item => {
        arr.push(item.id);
      });
      switch (this.nowBatchOperation) {
        case "":
          this.msg("", "error");
          break;
        case "":
          this.remove(arr);
          break;
        case "":
          this.remove(arr);
          break;
      }
    },
    //
    exportExcel() {
      let time = new Date().getTime();
      /* generate workbook object from table */
      var wb = XLSX.utils.table_to_book(document.querySelector("#out-table"));
      /* get binary string as output */
      var wbout = XLSX.write(wb, {
        bookType: "xlsx",
        bookSST: true,
        type: "array"
      });
      try {
        FileSaver.saveAs(
          new Blob([wbout], { type: "application/octet-stream" }),
          time + ".xlsx"
        );
      } catch (e) {
        if (typeof console !== "undefined") console.log(e, wbout);
      }
      return wbout;
    }
  }
};
</script>


<style lang="scss" scoped>
@import "../../assets/scss/mixin.scss";

div.order-list {
  @include table();

  //dialog
  .el-dialog {
    p {
      font-size: 16px;
      color: rgba(89, 87, 87, 1);
      line-height: 48px;
    }
  }
}
</style>
