<template>
  <div class="ad-list">
    <!--  -->
    <div class="product-list-wrapper">
      <!--  -->
      <header class="table-header">
        <div class="table-header-left"></div>
        <div class="table-header-right">
          <label class="label">:</label>
          <el-select
            v-model="pageSize"
            clearable
            placeholder=""
            class="margin-26"
            @change="handlePageSize"
          >
            <el-option v-for="i in 10" :key="i" :label="i" :value="i"/>
          </el-select>
          <el-button @click="$router.push('add-ad')"></el-button>
        </div>
      </header>
      <!--  -->
      <el-table
        ref="productTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column prop="id" label="" width/>
        <el-table-column prop="adName" label="" width/>
        <el-table-column prop="adPosition" label="" width>
          <template slot-scope="scope">{{scope.row.adPosition}}</template>
        </el-table-column>
        <el-table-column prop="goodsNum" label="">
          <template slot-scope="scope">
            <img :src="scope.row.adPicture" alt>
          </template>
        </el-table-column>
        <el-table-column prop="online" label="" width>
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.online"
              inactive-value="0"
              active-value="1"
              active-color="#5BC0BF"
              @change="handleShowStatus($event,scope.row)"
            />
            {{scope.row.online == 1 ? '' : ''}}
          </template>
        </el-table-column>
        <el-table-column prop="clickNumber" label="" width/>
        <el-table-column prop="name" label="" width="230">
          <template slot-scope="scope">
            <span @click="$router.push({name: 'add-ad',query:{adId:scope.row.id}})"></span>
            <span @click="top(scope.row)">{{scope.row.topState == 0 ? '' : ''}}</span>
            <span class="warning" @click="remove(scope.row.id)"></span>
          </template>
        </el-table-column>
      </el-table>
      <!-- + -->
      <footer>
        <div class="select-all-wrapper"/>
        <div class="pagination-wrapper">
          <pagination
            :totalPage="totalPage"
            :dataCount="dataCount"
            :pageSize="pageSize"
            :currentPage="currentPage"
            @pageChange="pageChange"
          />
        </div>
      </footer>
    </div>
  </div>
</template>

<script>
import mixin from "@/util/mixin.js";
import Pagination from "../common/pagination";

export default {
  mixins: [mixin],
  components: {
    Pagination
  },
  data() {
    return {};
  },
  created() {
    this.getTableData();
  },
  methods: {
    getTableData(page, id) {
      // 
      this.$axios({
        method: "post",
        url: "api/merchant_ad/get_merchant_ad_list",
        data: {
          currentPage: page || this.currentPage,
          pageSize: this.pageSize
        }
      })
        .then(res => {
          // console.log(res);
          this.tableData = res.data.data.list;
          this.totalPage = res.data.data.totalPage;
          this.dataCount = res.data.data.totalCount;
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    //
    addAd(id) {
      this.$router.push({
        name: "add-type",
        query: { parentId: id }
      });
    },
    //
    handleShowStatus($event, data) {
      // let show = $event ? 1 : 0;
      this.$axios({
        method: "post",
        url: "api/merchant_ad/change_merchant_ad",
        data: {
          id: data.id,
          online: $event
        }
      })
        .then(res => {
          this.msg("");
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    //
    top(data) {
      let top = data.topState == 0 ? 1 : 0;
      this.$axios({
        method: "post",
        url: "api/merchant_ad/change_merchant_ad",
        data: {
          id: data.id,
          topState: top
        }
      })
        .then(res => {
          this.msg("");
          this.getTableData();
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    // 
    remove(ids) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$axios({
          method: "post",
          url: "api/merchant_ad/delete_merchant_ad",
          data: { remark: ids }
        })
          .then(() => {
            this.msg("");
            this.getTableData();
          })
          .catch(err => {
            // console.log(err);
            this.msg(err, "error");
            this.getTableData();
          });
      });
    },
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/scss/mixin.scss";

.ad-list {
  @include table();

  .el-dialog {
    p {
      height: 60px;
      &:first-child > span {
        margin-right: 14px;
      }
    }
    .el-select {
      margin-left: 20px;
    }
  }
}
</style>
