<template>
  <div class="check-pics">
    <!-- title -->
    <div class="view-title">
      <i class="view-title-before"/>
      <span class="title"></span>
      <div class="refresh-btn">
        <el-button icon="el-icon-close" @click="$router.go(-1)"></el-button>
      </div>
    </div>
    <div class="main-wrapper">
      <!--  -->
      <div class="product-list-wrapper">
        <!--  -->
        <header class="table-header">
          <div class="table-header-left"></div>
          <div class="table-header-right">
            <el-button @click="dialogVisible=true"></el-button>
          </div>
        </header>
        <!--  -->
        <div class="main-table">
          <el-checkbox-group
            v-model="tableSelection"
            ref="productTable"
            @change="handleSelectionChange"
          >
            <div v-for="item in tableData" :key="item.id" class="check-item">
              <img :src="item.imgUrl" alt>
              <el-checkbox :label="item.id"/>
              <div class="item-operation">
                <span @click="transPics(item.id)"></span>
                <span @click="remove([item.id])"></span>
              </div>
            </div>
          </el-checkbox-group>
        </div>
        <!-- + -->
        <footer>
          <div class="select-all-wrapper">
            <el-checkbox
              :indeterminate="isIndeterminate"
              v-model="checkAll"
              @change="checkAllChange"
            ></el-checkbox>
            <el-select v-model="nowBatchOperation" clearable placeholder="" class="margin">
              <el-option v-for="item in batchOperation" :key="item" :value="item" :label="item"/>
            </el-select>
            <el-button @click="batchOperate"></el-button>
          </div>
          <div class="pagination-wrapper">
            <pagination
              :totalPage="totalPage"
              :dataCount="dataCount"
              :pageSize="pageSize"
              :currentPage="currentPage"
              @pageChange="pageChange"
            />
          </div>
        </footer>
      </div>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogVisible" width="30%">
        <el-form
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
          v-loading="loading"
        >
          <el-form-item label="" prop="goodsGalleriesId">
            <el-select v-model="ruleForm.goodsGalleriesId" placeholder="">
              <el-option
                v-for="item in goodsGalleriesList"
                :key="item.name"
                :label="item.name"
                :value="item.id"
              />
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="logo">
            <div class="upload-pics-wrapper" v-if="ruleForm.imgUrl.length">
              <div class="pic-wrapper" v-for="(img,index) in ruleForm.imgUrl" :key="img">
                <img :src="img" alt>
                <i class="el-icon-circle-close-outline" @click="ruleForm.imgUrl.splice(index,1)"/>
              </div>
            </div>
            <el-upload
              class="upload-demo"
              action="url"
              :http-request="uploadFile"
              accept="image/jpeg, image/png"
            >
              <el-button type="primary"></el-button>
              <div slot="tip" class="el-upload__tip"> (5)</div>
            </el-upload>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="submitRuleForm('ruleForm')"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogVisible1" width="30%">
        <el-form
          :model="ruleForm1"
          :rules="rules1"
          ref="ruleForm1"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="changeToGalleries">
            <el-select v-model="ruleForm1.changeToGalleries" placeholder="">
              <el-option
                v-for="item in goodsGalleriesList"
                :key="item.name"
                :label="item.name"
                :value="item.id"
              />
            </el-select>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible1 = false"> </el-button>
          <el-button type="primary" @click="submitRuleForm1('ruleForm1')"> </el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
//axiosqsrequest
import Qs from "qs";
import mixin from "@/util/mixin.js";
import Pagination from "../common/pagination";

export default {
  mixins: [mixin],
  components: {
    Pagination
  },
  data() {
    return {
      // dialogLoad: false,
      isAdd: true,
      picList: [],
      dialogVisible: false, //
      dialogVisible1: false, //
      goodsGalleriesList: [],
      ruleForm1: {
        //
        changeToGalleries: "",
        ids: [],
        subGalleries: ""
      },
      ruleForm: {
        //
        goodsGalleriesId: "",
        imgUrl: []
      },
      rules: {
        goodsGalleriesId: [
          { required: true, message: "", trigger: "change" }
        ],
        imgUrl: [{ required: true, message: "t", trigger: "change" }]
      },
      rules1: {
        goodsGalleriesId: [
          { required: true, message: "", trigger: "change" }
        ]
      }
    };
  },
  created() {
    this.getTableData();
    this.ruleForm.goodsGalleriesId = this.$route.query.picsId;
    this.ruleForm1.subGalleries = this.$route.query.picsId;
    this.ruleForm1.changeToGalleries = this.ruleForm1.subGalleries;
    //goodsGalleriesList
    this.$axios("api/merchant_goods_galleries/query")
      .then(res => {
        console.log(res);
        this.goodsGalleriesList = res.data.data.list;
      })
      .catch(err => {
        // console.log(err);
        this.msg(err, "error");
      });
    this.batchOperation.push("");
  },
  methods: {
    //
    getTableData(page) {
      this.$axios({
        method: "post",
        url: "api/merchant_goods_galleries_detail/query_for_page",
        data: {
          currentPage: page || this.currentPage,
          pageSize: this.pageSize,
          galleriesId: this.$route.query.picsId
        }
      })
        .then(res => {
          // console.log(res);
          this.tableData = res.data.data.list;
          this.totalPage = res.data.data.totalPage;
          this.dataCount = res.data.data.totalCount;
        })
        .catch(err => {
          // console.log(err);
          this.msg(err, "error");
        });
    },
    //el-upload
    //  logo
    uploadFile(content) {
      if (this.ruleForm.imgUrl.length >= 5) {
        this.msg("5", "error");
        return;
      }
      this.uploadImg(content.file).then(res => {
        // console.log(res);
        this.ruleForm.imgUrl.push(res.imgUrl);
      });
    },
    // 
    submitRuleForm(formName) {
      this.$refs[formName].validate(valid => {
        //
        if (valid) {
          // if (this.isAdd) {
          this.submitForm(
            "api/merchant_goods_galleries_detail/add_batch",
            this.ruleForm
          );
          // }
          this.msg();
          this.dialogVisible = false;
          this.getTableData();
        } else {
          this.msg("error submit!!", "error");
          return false;
        }
      });
    },
    //1 
    submitRuleForm1(formName) {
      this.$refs[formName].validate(valid => {
        //
        if (valid) {
          if (this.isAdd) {
            //
            this.submitForm(
              "api/merchant_goods_galleries_detail/change_galleries_batch",
              this.ruleForm1
            );
          }
          this.$nextTick(() => {
            this.msg();
            this.dialogVisible1 = false;
            this.getTableData();
          });
        } else {
          this.msg("error submit!!", "error");
          return false;
        }
      });
    },

    //
    transPics(id) {
      this.ruleForm1.ids = [];
      this.dialogVisible1 = true;
      this.ruleForm1.ids.push(id);
    },
    //
    checkAllChange($event) {
      let arr = [];
      this.tableData.forEach(item => {
        arr.push(item.id);
      });
      this.tableSelection = $event ? arr : [];
      this.isIndeterminate = false;
    },
    // 
    remove(ids) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$axios({
          method: "post",
          url: "api/merchant_goods_galleries_detail/delete_batch",
          data: ids
        })
          .then(() => {
            this.msg("");
            this.getTableData();
          })
          .catch(err => {
            this.msg(err, "error");
            this.getTableData();
          });
      });
    },
    //
    batchOperate() {
      if (!this.tableSelection.length) {
        this.msg("", "error");
        return;
      }
      switch (this.nowBatchOperation) {
        case "":
          this.msg("", "error");
          break;
        case "":
          this.remove(this.tableSelection);
          break;
        case "":
          this.dialogVisible1 = true;
          this.ruleForm1.ids = this.tableSelection;
          break;
      }
    }
  }
};
</script>

