<template>
  <div class="add-property">
    <!-- title -->
    <div class="view-title">
      <i class="view-title-before"/>
      <span class="title">{{titleTxt}}</span>
      <div class="refresh-btn" >
        <el-button icon="el-icon-close" @click="$router.go(-1)"></el-button>
      </div>
    </div>
    <div class="main-wrapper">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="paramName">
          <el-input v-model="ruleForm.paramName" placeholder=""/>
        </el-form-item>
        <el-form-item label="" prop="styleId">
          <el-select v-model="ruleForm.styleId" placeholder="">
            <el-option
              v-for="item in productTypes"
              :key="item.styleName"
              :label="item.styleName"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="ruleForm.paramSelect" :disabled="!isAdd">
            <el-radio :label="0"></el-radio>
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
          </el-radio-group>
          <p>"/"""</p>
        </el-form-item>
        <el-form-item label="" prop="paramList">
          <el-tag
            :key="tag"
            v-for="tag in paramList"
            closable
            :disable-transitions="false"
            @close="handleClose(tag)"
          >{{tag}}</el-tag>
          <el-input
            class="input-new-tag"
            v-if="inputVisible"
            v-model="inputValue"
            ref="saveTagInput"
            size="small"
            @keyup.enter.native="handleInputConfirm"
            @blur="handleInputConfirm"
          />
          <el-button v-else class="button-new-tag" size="small" @click="showInput">+ </el-button>
        </el-form-item>
        <el-form-item label="" prop="proprtySort">
          <el-input v-model="ruleForm.paramSort" type="number"/>
          <p></p>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitRuleForm('ruleForm')">{{titleTxt}}</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import mixin from "@/util/mixin.js";

export default {
  mixins: [mixin],
  data() {
    //this.paramList
    let validParamList = (rule, value, callback) => {
      if (this.paramList.length === 0) {
        callback(new Error(""));
      } else if (this.ruleForm.paramSelect == 0 && this.paramList.length > 1) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      isAdd: true,
      productTypes: [],
      inputVisible: false,
      inputValue: "",
      paramList: [], //this.paramListjoin()paramList
      ruleForm: {
        id: 0,
        styleId: "",
        paramName: "",
        paramList: [],
        paramSelect: 0
      },
      rules: {
        paramName: [
          { required: true, message: "", trigger: "blur" }
        ],
        styleId: [
          { required: true, message: "", trigger: "change" }
        ],
        paramList: [
          {
            validator: validParamList,
            required: true,
            trigger: "change"
          }
        ]
      }
    };
  },
  created() {
    if (this.$route.query.paramId) {
      this.isAdd = false;
      this.getparamData();
    }
    //
    this.$axios("api/merchantGoodsStyle/merchant_goods_type_list")
      .then(res => {
        // console.log(res);
        this.productTypes = res.data.data;
      })
      .catch(err => {
        this.msg(err, "error");
      });
  },
  computed: {
    titleTxt() {
      return this.isAdd ? "" : "";
    }
  },
  methods: {
    //data
    getparamData() {
      this.$axios({
        method: "post",
        url: "api/merchantGoodsParam/merchant_goods_type_by_id",
        data: {
          id: this.$route.query.paramId
        }
      })
        .then(res => {
          // console.log(res);
          this.ruleForm.id = this.$route.query.paramId;
          this.ruleForm.styleId = res.data.data.styleId;
          this.ruleForm.paramName = res.data.data.paramName;
          this.ruleForm.paramSelect = res.data.data.paramSelect;
          this.ruleForm.paramSort = res.data.data.paramSort;
          this.paramList = res.data.data.paramList.split(",");
        })
        .catch(err => {
          console.log(err);
        });
    },

    //
    handleClose(tag) {
      this.paramList.splice(this.paramList.indexOf(tag), 1);
    },
    //input
    showInput() {
      this.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    //
    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.paramList.push(inputValue);
      }
      this.inputVisible = false;
      this.inputValue = "";
    },
    //
    submitRuleForm(formName) {
      this.$refs[formName].validate(valid => {
        //
        if (valid) {
          this.ruleForm.paramList = this.paramList.join();
          if (this.isAdd) {
            //
            this.submitForm(
              "api/merchantGoodsParam/merchant_goods_type_add",
              this.ruleForm
            );
          } else {
            this.submitForm(
              "api/merchantGoodsParam/merchant_goods_type_update",
              this.ruleForm
            );
          }
          this.msg();
          this.goBack();
        } else {
          this.msg('error submit!!','error')
          return false;
        }
      });
    }
  }
};
</script>

