<template>
  <div class="order-setting">
    <!-- title -->
    <div class="view-title">
      <i class="view-title-before"/>
      <span class="title"></span>
      <div class="refresh-btn">
        <el-button icon="el-icon-close" @click="$router.go(-1)"></el-button>
      </div>
    </div>
    <!--  -->
    <div class="main-wrapper">
      <div class="center">
        <el-form
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="150px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="normalEndpayTime">
            <el-input v-model="ruleForm.normalEndpayTime" placeholder="" type="number">
              <template slot="append"></template>
            </el-input>
          </el-form-item>
          <el-form-item label="" prop="sendEndTime">
            <el-input v-model="ruleForm.sendEndTime" placeholder="" type="number">
              <template slot="append"></template>
            </el-input>
          </el-form-item>
          <el-form-item label="" prop="commentEndTime">
            <el-input v-model="ruleForm.commentEndTime" placeholder="" type="number">
              <template slot="append"></template>
            </el-input>
          </el-form-item>
          <el-form-item label="" prop="finalEndTime">
            <el-input v-model="ruleForm.finalEndTime" placeholder="" type="number">
              <template slot="append"></template>
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitRuleForm('ruleForm')"></el-button>
          </el-form-item>
        </el-form>
        <div class="form-right">
          <p></p>
          <p></p>
          <p></p>
          <p></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import mixin from "@/util/mixin.js";

export default {
  mixins: [mixin],
  data() {
    return {
      fileList: [],
      ruleForm: {
        commentEndTime: "",
        finalEndTime: "",
        id: "",
        merchantId: "",
        normalEndpayTime: "",
        seckillEndpayTime: "",
        sendEndTime: ""
      },
      rules: {
        commentEndTime: [
          { required: true, message: "", trigger: "blur" }
        ],
        finalEndTime: [
          { required: true, message: "", trigger: "blur" }
        ],
        normalEndpayTime: [
          { required: true, message: "", trigger: "blur" }
        ],
        sendEndTime: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.getFormData();
  },
  methods: {
    //data
    getFormData() {
      this.$axios({
        method: "post",
        url: "api/merchant_order_set/queryOrderSet",
        data: {
          merchantId: JSON.parse(localStorage.user).merchantId
        }
      })
        .then(res => {
          // console.log(res);
          this.ruleForm = res.data.data;
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },

    //
    submitRuleForm(formName) {
      this.$refs[formName].validate(valid => {
        //
        if (valid) {
          this.submitForm(
            "api/merchant_order_set/add_or_update",
            this.ruleForm
          );
          this.$message({
            message: "",
            type: "success",
            showClose: true,
            duration: 2500
          });
          setTimeout(() => {
            this.$router.back();
          }, 3000);
        } else {
          this.msg("error submit!!", "error");
          return false;
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/mixin.scss";

.order-setting {
  @include title();
  //
  .main-wrapper {
    padding: 37px 45px 0 45px;
    display: flex;
    justify-content: center;
    .center {
      display: flex;
    }
    .form-right {
      p {
        color: #595757;
        line-height: 40px;
        margin-left: 10px;
        margin-bottom: 23px;
      }
    }
  }
}
</style>
