<script>
export default {
  data() {
    return {
      shortcuts: [
        { title: "", name: "add", path:'/product/add-product'},
        { title: "", name: "order", path:'/order/order-list' },
        { title: "", name: "users", path:'/account/account-list' },
        { title: "", name: "data", path:'' },
        // { title: "", name: "msg" },
        { title: "", name: "ad", path:'/operation/ad-list' },
        // { title: "", name: "heart", path:'/product/add-product' },
        // { title: "", name: "flash" },
        { title: "", name: "good", path:'' }
      ],
      overall: [
        {
          title: "",
          proOverall: ["", "", "", ""]
        },
        {}
      ],
      orderData: {},
      productData: {
        notPull: "0",
        put: "0",
        count: "0",
        all: "0"
      },
      userData: {}
    };
  },
  created() {
    this.getOrderData();
    this.getProductData();
    this.getUserData();
  },
  methods: {
    //
    getOrderData() {
      this.$axios({
        method: "post",
        url: "api/merchant_order/getOrderAllCount",
        data: {
          merchantId: JSON.parse(localStorage.user).merchantId
        }
      })
        .then(res => {
          // console.log(res);
          this.orderData = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    getProductData() {
      this.$axios("api/merchantGoods/goodsCountStatistics")
        .then(res => {
          // console.log(res);
          if (res.data.data) {
            this.productData = res.data.data;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    getUserData() {
      this.$axios("api/merchant/merchant_index")
        .then(res => {
          // console.log(res);
          this.userData = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

