<template>
  <div class="rating">
    <!--  -->
    <div class="search-box">
      <div class="search-top">
        <div class="search-box-left">
          <span>
            <i class="el-icon-search"/>
          </span>
        </div>
        <div class="search-box-right">
          <span @click="searchShow">
            <i :class="showSearch ? 'el-icon-arrow-up' : 'el-icon-arrow-down'"/>
            {{showSeachTxt}}
          </span>
          <el-button @change="getTableData(1)"></el-button>
        </div>
      </div>
      <div class="search-bottom" v-if="showSearch">
        <div class="search-bar">
          <el-input v-model="goodsName" placeholder="/" clearable @change="getTableData(1)"/>
        </div>
        <div class="search-bar">
          <el-input v-model="userName" placeholder="/" clearable @change="getTableData(1)"/>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="product-list-wrapper">
      <!--  -->
      <header class="table-header">
        <div class="table-header-left"></div>
        <div class="table-header-right">
          <label class="label">:</label>
          <el-select
            v-model="pageSize"
            clearable
            placeholder=""
            class="margin-26"
            @change="handlePageSize"
          >
            <el-option v-for="i in 10" :key="i" :label="i" :value="i"/>
          </el-select>
          <!-- <el-select v-model="nowSort" clearable placeholder="">
            <el-option v-for="item in sort" :key="item" :label="item" :value="item"/>
          </el-select>-->
        </div>
      </header>
      <!--  -->
      <el-table
        ref="productTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="56"/>
        <el-table-column label="" width>
          <template slot-scope="scope">{{scope.row.id}}</template>
        </el-table-column>
        <el-table-column prop="name" label="" width>
          <template slot-scope="scope">{{scope.row.userName}}</template>
        </el-table-column>
        <el-table-column prop="address" label="" width>
          <template slot-scope="scope">
            <p>{{scope.row.goodsName}}</p>
          </template>
        </el-table-column>
        <el-table-column prop="commentLevel" label="" width="150">
          <template slot-scope="scope">
            <el-rate v-model="scope.row.commentLevel" disabled></el-rate>
          </template>
        </el-table-column>
        <el-table-column prop="commentIp" label="IP" width>
          <template slot-scope="scope">{{scope.row.commentIp}}</template>
        </el-table-column>
        <el-table-column prop="createTime" label="" width>
          <template slot-scope="scope">{{scope.row.createTime}}</template>
        </el-table-column>
        <el-table-column prop="name" label="" width>
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.status"
              :active-value="0"
              :inactive-value="2"
              active-color="#5BC0BF"
              @change="handleStatus(scope.row.id, $event)"
            />
          </template>
        </el-table-column>
        <el-table-column prop="name" label="" width>
          <template slot-scope="scope">
            <span @click="showRatingDetail(scope.row.id)"></span>
            <span class="warning" @click="remove([scope.row.id])"></span>
          </template>
        </el-table-column>
      </el-table>
      <!-- + -->
      <footer>
        <div class="select-all-wrapper">
          <el-checkbox
            :indeterminate="isIndeterminate"
            v-model="checkAll"
            @change="handleCheckAllChange"
          ></el-checkbox>
          <el-select v-model="nowBatchOperation" clearable placeholder="" class="margin">
            <el-option v-for="item in batchOperation" :key="item" :value="item" :label="item"/>
          </el-select>
          <el-button @click="batchOperate"></el-button>
        </div>
        <div class="pagination-wrapper">
          <pagination
            :totalPage="totalPage"
            :dataCount="dataCount"
            :pageSize="pageSize"
            :currentPage="currentPage"
            @pageChange="pageChange"
          />
        </div>
      </footer>
    </div>
  </div>
</template>

<script>
import mixin from "@/util/mixin.js";
import Pagination from "../common/pagination";

export default {
  mixins: [mixin],
  components: {
    Pagination
  },
  data() {
    return {
      // userName: ""
    };
  },
  created() {
    //
    this.getTableData(1);
    //
    this.$axios("api/merchantGoodsType/query_goods_type_tree")
      .then(res => {
        // console.log(res);
        this.productTypes = res.data.data;
      })
      .catch(err => {
        console.log(err);
      });
  },
  methods: {
    //
    getTableData(page) {
      this.$axios({
        method: "post",
        url: "api/merchantGoodsComment/merchant_goods_type_list_page",
        data: {
          currentPage: page || this.currentPage,
          pageSize: this.pageSize,
          goodsName: this.goodsName,
          userName: this.userName
        }
      })
        .then(res => {
          // console.log(res);
          this.tableData = res.data.data.list;
          this.totalPage = res.data.data.totalPage;
          this.dataCount = res.data.data.totalCount;
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    //
    handleStatus(id, $event) {
      this.$axios({
        method: "post",
        url: "api/merchantGoodsComment/delete_not_show",
        data: {
          id: id,
          status: $event
        }
      })
        .then(res => {
          this.msg(res);
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    showRatingDetail(id) {
      this.$router.push({
        name: "rating-detail",
        params: {
          userId: id
        }
      });
    },
    // (+)
    remove(ids) {
      //id[]
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$axios({
          method: "post",
          url: "api/merchantGoodsComment/delete_batch",
          data: ids
        })
          .then(() => {
            this.msg("");
            this.getTableData();
          })
          .catch(err => {
            // console.log(err);
            this.msg(err, "error");
            this.getTableData();
          });
      });
    },
    batchOperate() {
      let arr = [];
      this.tableSelection.forEach(item => {
        arr.push(item.id);
      });
      switch (this.nowBatchOperation) {
        case "":
          this.msg("", "error");
          break;
        case "":
          this.remove(arr);
          break;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/scss/mixin.scss";

div.rating {
  @include table();
}
</style>
