<template>
  <div class="return-detail">
    <div class="main-wrapper">
      <!--  -->
      <div class="product-list-wrapper">
        <!--  -->
        <header class="table-header">
          <div class="table-header-left"></div>
        </header>
        <!--  -->
        <el-table ref="productTable" :data="table1" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="goodsPic" label="" width>
            <template slot-scope="scope">
              <img :src="scope.row.goodsPic" alt>
            </template>
          </el-table-column>
          <el-table-column prop="goodsName" label="" width/>
          <el-table-column prop="goodsPrice" label="/" width/>
          <el-table-column prop="goodsBrand" label="" width/>
          <el-table-column prop="buyNum" label="" width/>
          <el-table-column prop="totalMoney" label="" width/>
        </el-table>
      </div>

      <div class="table-wrapper">
        <div class="header"></div>
        <table>
          <tr>
            <td></td>
            <td>{{tableData.code}}</td>
          </tr>
          <tr>
            <td></td>
            <td>{{returnStatus(tableData.status)}}</td>
          </tr>
          <tr>
            <td></td>
            <td>{{tableData.orderCode}}</td>
          </tr>
          <tr>
            <td></td>
            <td>{{tableData.creatTime}}</td>
          </tr>
          <tr>
            <td></td>
            <td>{{tableData.mobilePhone}}</td>
          </tr>
          <tr>
            <td></td>
            <td>{{tableData.contact}}</td>
          </tr>
          <tr>
            <td></td>
            <td>{{tableData.phone}}</td>
          </tr>
          <tr>
            <td></td>
            <td>{{tableData.returnReason}}</td>
          </tr>
          <tr>
            <td></td>
            <td>{{tableData.problemDescription}}</td>
          </tr>
          <tr>
            <td></td>
            <td>
              <div class="pic" v-for="img in problemPics" :key="img">
                <img :src="img" alt>
              </div>
            </td>
          </tr>
        </table>
        <table>
          <tr>
            <td></td>
            <td>{{tableData.applicationReturnMoney}}</td>
          </tr>
          <tr>
            <td></td>
            <td>
              <el-input
                v-if="tableData.status== 1"
                v-model="returnMoney"
                type="number"
                step="0.01"
                min="0"
              />
              <span v-if="tableData.status !== 1">{{tableData.returnMoney}}</span>
            </td>
          </tr>
          <tr v-if="tableData.status== 1">
            <td></td>
            <td>
              <el-select v-model="selectId" @change="getSelectIdData" placeholder="">
                <el-option
                  v-for="item in returnSiteList"
                  :key="item.id"
                  :label="item.returnPname"
                  :value="item.id"
                ></el-option>
              </el-select>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>{{tableData.returnName || selectSite.returnName}}</td>
          </tr>
          <tr>
            <td></td>
            <td>{{tableData.returnAddress || selectSite.returnAddress}}</td>
          </tr>
          <tr>
            <td></td>
            <td>{{tableData.returnPhone || selectSite.returnPhone}}</td>
          </tr>
        </table>
        <table>
          <tr>
            <td></td>
            <td>
              <el-input v-model="resolveRemark" v-if="tableData.status == 1"/>
              <span v-if="tableData.status !== 1">{{tableData.resolveRemark}}</span>
            </td>
          </tr>
        </table>
      </div>
      <div class="btn-group">
        <el-button v-if="tableData.status== 1" @click="agreeOrRejectReturn(2)"></el-button>
        <el-button type="danger" v-if="tableData.status== 1" @click="agreeOrRejectReturn(3)"></el-button>
        <el-button v-if="tableData.status !== 1" @click="$router.go(-1)"></el-button>
      </div>
    </div>
  </div>
</template>

<script>
import mixin from "@/util/mixin.js";

export default {
  mixins: [mixin], //vue 
  data() {
    return {
      table1: [],
      returnMoney: "",
      returnSiteList: [],
      selectId: "",
      selectSite: "",
      resolveRemark: ""
    };
  },

  created() {
    this.getTableData();
  },
  computed: {
    //
    problemPics() {
      let picArr = [];
      if (this.tableData.problemPhoto) {
        if (this.tableData.problemPhoto.indexOf(",") !== -1) {
          picArr = this.this.tableData.problemPhoto.split(",");
        } else {
          picArr.push(this.tableData.problemPhoto);
        }
      }
      return picArr;
    }
  },
  methods: {
    //
    getTableData() {
      //
      this.$axios({
        method: "post",
        url: "api/merchant_return_goods/query_By_Id",
        data: {
          id: this.$route.query.orderId,
          merchantId: JSON.parse(localStorage.user).merchantId
        }
      })
        .then(res => {
          console.log(res);
          //
          this.tableData = res.data.data;
          //
          this.table1.push(res.data.data.merchantOrderDetail);
        })
        .catch(err => {
          console.log(err);
        });
      //
      this.$axios({
        method: "post",
        url: "api/return_send/query_for_page",
        data: {
          currentPage: this.currentPage,
          pageSize: this.pageSize,
          merchantId: JSON.parse(localStorage.user).merchantId
        }
      })
        .then(res => {
          console.log(res);
          this.returnSiteList = res.data.data.list;
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    returnStatus(status) {
      //0123 45 6 20
      switch (status) {
        case 1:
          return "";
          break;
        default:
          return "";
      }
    },
    //
    getSelectIdData() {
      this.$axios({
        method: "post",
        url: "api/return_send/query_By_Id",
        data: {
          id: this.selectId
        }
      })
        .then(res => {
          console.log(res);
          this.selectSite = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    agreeOrRejectReturn(status) {
      this.$axios({
        method: "post",
        url: "api/merchant_return_goods/update",
        data: {
          id: this.tableData.id,
          status: status,
          returnSendId: this.selectId,
          returnMoney: this.returnMoney,
          resolveRemark: this.resolveRemark
        }
      }).then(
        res => {
          this.$message({
            message: "",
            type: "success",
            showClose: true,
            duration: 2500
          });
          setTimeout(() => {
            this.$router.back();
          }, 3000);
        },
        err => {}
      );
    },
  }
};
</script>

