<script>
import mixin from "@/util/mixin.js";
import Pagination from "../common/pagination";

export default {
  mixins: [mixin], //vue 
  components: {
    Pagination
  },
  data() {
    return {
      dialogVisible: false,
      dialogStatus: 1,
      ruleForm: {
        id: "",
        receiver: "",
        phone: "",
        address: ""
      },
      rules: {
        receiver: [
          { required: true, message: "", trigger: "blur" }
        ],
        phone: [{ required: true, message: "", trigger: "blur" }],
        address: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.getTableData();
  },
  computed: {
    statusTxt() {
      //0123 45 6 20
      switch (this.tableData[0].status) {
        case 0:
          return "";
          break;
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case 3:
          return "";
          break;
        case 4:
          return "";
          break;
        case 5:
          return "";
          break;
        case 6:
          return "";
          break;
        case 20:
          return "";
          break;
      }
    },
    dialogTitle() {
      return this.dialogStatus ? "" : "";
    }
  },
  methods: {
    //data
    getTableData() {
      this.$axios({
        method: "post",
        url: "api/merchant_order/query_By_Id",
        data: {
          id: this.$route.query.orderId,
          merchantId: JSON.parse(localStorage.user).merchantId
        }
      })
        .then(res => {
          console.log(res);
          this.tableData.push(res.data.data);
          this.ruleForm.id = res.data.data.id;
          this.ruleForm.receiver = res.data.data.receiver;
          this.ruleForm.phone = res.data.data.phone;
          this.ruleForm.address = res.data.data.address;
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    //
    showDialog(i) {
      this.dialogVisible = true;
      this.dialogStatus = i;
    },
    //
    submitRuleForm(formName) {
      this.$refs[formName].validate(valid => {
        //
        if (valid) {
          this.submitForm("api/merchant_order/updateOrder", this.ruleForm);
          this.msg("");
          setTimeout(() => {
            this.$router.back();
          }, 50);
        } else {
          this.msg("error submit!!", "error");
          return false;
        }
      });
    }
  }
};
</script>

