<template>
  <div class="return-reason">
    <!--  -->
    <div class="product-list-wrapper">
      <!--  -->
      <header class="table-header">
        <div class="table-header-left"></div>
        <div class="table-header-right">
          <label class="label">:</label>
          <el-select
            v-model="pageSize"
            clearable
            placeholder=""
            class="margin-26"
            @change="handlePageSize"
          >
            <el-option v-for="i in 10" :key="i" :label="i" :value="i"/>
          </el-select>
          <el-button @click="editReason(0)"></el-button>
        </div>
      </header>
      <!--  -->
      <el-table
        ref="productTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="56"/>
        <el-table-column prop="reasonType" label="" width/>
        <el-table-column prop="sorts" label="" width/>
        <el-table-column prop="showStatus" label="" width>
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.isUse"
              :inactive-value="0"
              :active-value="1"
              active-color="#5BC0BF"
              @change="handleShowStatus($event,scope.row)"
            />
          </template>
        </el-table-column>
        <el-table-column prop="creatTime" label=""/>
        <el-table-column prop="oeration" label="" width>
          <template slot-scope="scope">
            <span @click="editReason(scope.row)"></span>
            <span class="warning" @click="remove([scope.row.id])"></span>
          </template>
        </el-table-column>
      </el-table>
      <!-- + -->
      <footer>
        <div class="select-all-wrapper">
          <el-checkbox
            :indeterminate="isIndeterminate"
            v-model="checkAll"
            @change="handleCheckAllChange"
          ></el-checkbox>
          <el-select v-model="nowBatchOperation" clearable placeholder="" class="margin">
            <el-option v-for="item in batchOperation" :key="item" :value="item" :label="item"/>
          </el-select>
          <el-button @click="batchOperate"></el-button>
        </div>
        <div class="pagination-wrapper">
          <pagination
            :totalPage="totalPage"
            :dataCount="dataCount"
            :pageSize="pageSize"
            :currentPage="currentPage"
            @pageChange="pageChange"
          />
        </div>
      </footer>
    </div>

    <!--  -->
    <el-dialog :title="title" :visible.sync="dialogVisible" width="30%">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="reasonType">
          <el-input v-model="ruleForm.reasonType" placeholder=""/>
        </el-form-item>
        <el-form-item label="" prop="sorts">
          <el-input v-model="ruleForm.sorts" placeholder="" type="number"/>
        </el-form-item>
        <el-form-item label="" prop="isUse">
          <el-switch
            v-model="ruleForm.isUse"
            :inactive-value="0"
            :active-value="1"
            active-color="#5BC0BF"
          />
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="submitRuleForm(ruleForm)"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import mixin from "@/util/mixin.js";
import Pagination from "../common/pagination";

export default {
  mixins: [mixin],
  components: {
    Pagination
  },
  data() {
    return {
      isAdd: false,
      dialogVisible: false,
      //
      ruleForm: {
        isUse: 1,
        reasonType: "",
        sorts: ""
      },
      rules: {
        reasonType: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.getTableData();
  },
  computed: {
    title() {
      return this.isAdd ? "" : "";
    }
  },
  methods: {
    getTableData(page, id) {
      // 
      this.$axios({
        method: "post",
        url: "api/return_reason/query_for_pageB",
        data: {
          currentPage: page || this.currentPage,
          pageSize: this.pageSize,
          merchantId: JSON.parse(localStorage.user).merchantId
        }
      })
        .then(res => {
          // console.log(res);
          this.tableData = res.data.data.list;
          this.totalPage = res.data.data.totalPage;
          this.dataCount = res.data.data.totalCount;
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    //
    handleShowStatus($event, data) {
      let show = $event ? 1 : 0;
      this.$axios({
        method: "post",
        url: "api/return_reason/add",
        data: {
          id: data.id,
          isUse: show,
          merchantId: JSON.parse(localStorage.user).merchantId
        }
      })
        .then(res => {
          //console.log(res);
          this.msg("");
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    //
    editReason(data) {
      this.dialogVisible = true;
      this.isAdd = data ? false : true;
      this.ruleForm = data ? data : this.ruleForm;
    },
    submitRuleForm(formName) {
      if (this.isAdd) {
        this.submitForm("api/return_reason/add", this.ruleForm, {
          merchantId: JSON.parse(localStorage.user).merchantId
        });
        this.msg("");
        setTimeout(() => {
          this.dialogVisible = false;
          this.getTableData();
        }, 50);
      } else {
        this.submitForm("api/return_reason/add", this.ruleForm);
        this.msg("");
        setTimeout(() => {
          this.dialogVisible = false;
          this.getTableData();
        }, 50);
      }
    },
    //
    remove(ids) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$axios({
          method: "post",
          url: "api/return_reason/delete",
          data: {
            ids: ids
          }
        })
          .then(() => {
            this.msg("");
            this.getTableData();
          })
          .catch(err => {
            this.msg(err, "error");
          });
      });
    },
    //
    batchOperate() {
      let arr = [];
      this.tableSelection.forEach(item => {
        arr.push(item.id);
      });
      switch (this.nowBatchOperation) {
        case "":
          this.msg("", "error");
          break;
        case "":
          this.remove(arr);
          break;
        case "":
          this.remove(arr);
          break;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/scss/mixin.scss";

.return-reason {
  @include table();

  .el-dialog {
    p {
      display: flex;
      height: 60px;
      &:first-child > span {
        margin-right: 14px;
      }
      input {
        width: 100px;
      }
    }
    .el-select {
      margin-left: 20px;
    }
  }
}
</style>
