<template>
  <div class="add-brand">
    <!-- title -->
    <div class="view-title">
      <i class="view-title-before"/>
      <span class="title">{{titleTxt}}</span>
      <div class="refresh-btn">
        <el-button icon="el-icon-close" @click="$router.go(-1)"></el-button>
      </div>
    </div>
    <!--  -->
    <div class="main-wrapper">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="150px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name" placeholder=""/>
        </el-form-item>
        <el-form-item label="" prop="firstChar">
          <el-input v-model="ruleForm.firstChar" placeholder=""/>
        </el-form-item>
        <el-form-item label="LOGO" prop="logo">
          <img :src="logo" alt v-if="ruleForm.logo">
          <el-upload
            class="upload-demo"
            action="url"
            :http-request="uploadFile"
            accept="image/jpeg, image/png"
          >
            <el-button type="primary"></el-button>
            <div slot="tip" class="el-upload__tip">jpg/png</div>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="areaLogo">
          <img :src="ruleForm.areaLogo" alt v-if="ruleForm.areaLogo">
          <el-upload
            class="upload-demo"
            action="url"
            :http-request="uploadAreaFile"
            accept="image/jpeg, image/png"
          >
            <el-button type="primary"></el-button>
            <div slot="tip" class="el-upload__tip">jpg/png</div>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="sorting">
          <el-input v-model="ruleForm.sorting" type="number"/>
        </el-form-item>
        <el-form-item label="" prop="story">
          <el-input v-model="ruleForm.story" type="textarea"/>
        </el-form-item>
        <el-form-item label="" prop="isShow">
          <el-radio-group v-model="ruleForm.isShow">
            <el-radio :label="1"></el-radio>
            <el-radio :label="0"></el-radio>
            <p></p>
          </el-radio-group>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitRuleForm('ruleForm')">{{titleTxt}}</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
//axiosqsrequest
import Qs from "qs";
import mixin from "@/util/mixin.js";

export default {
  mixins: [mixin],
  data() {
    return {
      isAdd: true,
      fileList: [],
      ruleForm: {
        areaLogo: "",
        // code: "",
        firstChar: "",
        // goodsTotal: "",
        // isManufacturer: "",
        isShow: 1,
        logo: "",
        name: "",
        sorting: ""
      },
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        logo: [{ required: true, message: "logo", trigger: "change" }],
        isShow: [
          { required: true, message: "", trigger: "change" }
        ]
      }
    };
  },
  created() {
    if (this.$route.query.brandId) {
      this.isAdd = false;
      this.getFormData();
    } else {
      this.isAdd = true;
    }
  },
  computed: {
    titleTxt() {
      return this.isAdd ? "" : "";
    },
    logo() {
      //http
      let idx = this.ruleForm.logo.lastIndexOf("http");
      return this.ruleForm.logo.substr(idx);
    }
  },
  methods: {
    //data
    getFormData() {
      this.$axios({
        method: "post",
        url: "api/merchant_goods_brand/query_by_id",
        type: "form",
        data: {
          id: this.$route.query.brandId
        },
        //qsdataform
        transformRequest: [
          function(data) {
            data = Qs.stringify(data);
            return data;
          }
        ],
        // headerformdata
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      })
        .then(res => {
          console.log(res);
          this.ruleForm = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    //  logo
    uploadFile(content) {
      this.uploadImg(content.file).then(res => {
        // console.log(res);
        this.ruleForm.logo = res.imgUrl;
      });
    },
    //  areaLogo
    uploadAreaFile(content) {
      this.uploadImg(content.file).then(res => {
        // console.log(res);
        this.ruleForm.areaLogo = res.imgUrl;
      });
    },

    //
    submitRuleForm(formName) {
      this.$refs[formName].validate(valid => {
        //
        if (valid) {
          if (this.isAdd) {
            //
            this.submitForm("api/merchant_goods_brand/add", this.ruleForm);
          } else {
            this.submitForm("api/merchant_goods_brand/update", this.ruleForm);
          }
          this.msg();
          this.goBack();
        } else {
          this.msg("error submit!!", "error");
          return false;
        }
      });
    }
  }
};
</script>

