<template>
  <div class="add-pic">
    <!-- title -->
    <div class="view-title">
      <i class="view-title-before"/>
      <span class="title">{{titleTxt}}</span>
      <div class="refresh-btn">
        <el-button icon="el-icon-close" @click="$router.go(-1)"></el-button>
      </div>
    </div>
    <!--  -->
    <div class="main-wrapper">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name" placeholder=""/>
        </el-form-item>
        <el-form-item label="" prop="cover">
          <img :src="ruleForm.cover" alt v-if="ruleForm.cover">
          <el-upload
            class="upload-demo"
            action="url"
            :http-request="uploadFile"
            accept="image/jpeg, image/png"
          >
            <el-button type="primary"></el-button>
            <div slot="tip" class="el-upload__tip">jpg/png200kb</div>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="sorting">
          <el-input v-model="ruleForm.sorting" type="number"/>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input v-model="ruleForm.description" type="textarea"/>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitRuleForm('ruleForm')">{{titleTxt}}</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
//axiosqsrequest
import Qs from "qs";
import mixin from "@/util/mixin.js";

export default {
  mixins: [mixin],
  data() {
    return {
      isAdd: true,
      fileList: [],
      ruleForm: {
        cover: "",
        description: "",
        name: "",
        sorting: "",
      },
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        cover: [{ required: true, message: "", trigger: "change" }]
      }
    };
  },
  created() {
    if (this.$route.query.picsId) {
      this.isAdd = false;
      this.getFormData();
    } else {
      this.isAdd = true;
    }
  },
  computed: {
    titleTxt() {
      return this.isAdd ? "" : "";
    }
  },
  methods: {
    //data
    getFormData() {
      this.$axios({
        method: "post",
        url: "api/merchant_goods_galleries/query_by_id",
        type: "form",
        data: {
          id: this.$route.query.picsId
        },
        //qsdataform
        transformRequest: [
          function(data) {
            data = Qs.stringify(data);
            return data;
          }
        ],
        // headerformdata
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      })
        .then(res => {
          // console.log(res);
          this.ruleForm = res.data.data;
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    //  logo
    uploadFile(content) {
      this.uploadImg(content.file).then(res => {
        // console.log(res);
        this.ruleForm.cover = res.imgUrl;
      });
    },
    //
    submitRuleForm(formName) {
      this.$refs[formName].validate(valid => {
        //
        if (valid) {
          console.log(this.isAdd)
          if (this.isAdd) {
            //
            this.submitForm("api/merchant_goods_galleries/add", this.ruleForm);
          } else {
            this.submitForm(
              "api/merchant_goods_galleries/update",
              this.ruleForm
            );
          }
          this.msg();
          this.goBack();
        } else {
          this.msg("error submit!!", "error");
          return false;
        }
      });
    }
  }
};
</script>

