<template>
  <div class="types">
    <!--  -->
    <div class="product-list-wrapper">
      <!--  -->
      <header class="table-header">
        <div class="table-header-left"></div>
        <div class="table-header-right">
          <label class="label">:</label>
          <el-select
            v-model="pageSize"
            clearable
            placeholder=""
            class="margin-26"
            @change="handlePageSize"
          >
            <el-option v-for="i in 10" :key="i" :label="i" :value="i"/>
          </el-select>
          <!-- <el-select v-model="nowSort" clearable placeholder="">
            <el-option v-for="item in sort" :key="item" :label="item" :value="item"/>
          </el-select>-->
          <el-button @click="$router.push({name: 'add-type',query: {parentId: 0}})"></el-button>
          <el-button @click="gobackTable" v-if="isSecond"></el-button>
        </div>
      </header>
      <!--  -->
      <el-table
        ref="productTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column label="" prop="id" width/>
        <el-table-column prop="typeName" label="" width/>
        <el-table-column prop="parentId" label="" width>
          <template slot-scope="scope">{{scope.row.parentId ? '' : ''}}</template>
        </el-table-column>
        <el-table-column prop="goodsNum" label=""/>
        <el-table-column prop="goodsUnit" label="" width/>
        <el-table-column prop="showStatus" label="" width>
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.showStatus"
              :inactive-value="1"
              :active-value="0"
              active-color="#5BC0BF"
              @change="handleShowStatus($event,scope.row)"
            />
          </template>
        </el-table-column>
        <el-table-column prop="goodsSort" label="" width/>
        <el-table-column prop="name" label="" width>
          <template slot-scope="scope">
            <span @click="transType(scope.row)"></span>
            <span
              v-if="!isSecond"
              @click="$router.push({name: 'add-type',query: {parentId: scope.row.id}})"
            ></span>
            <span v-if="!isSecond" @click="toSecond(scope.row.id)"></span>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="" width>
          <template slot-scope="scope">
            <span @click="$router.push({name: 'add-type',query: {id: scope.row.id}})"></span>
            <span class="warning" @click="remove(scope.row.id)"></span>
          </template>
        </el-table-column>
      </el-table>
      <!-- + -->
      <footer>
        <div class="pagination-wrapper">
          <pagination
            :totalPage="totalPage"
            :dataCount="dataCount"
            :pageSize="pageSize"
            :currentPage="currentPage"
            @pageChange="pageChange"
          />
        </div>
      </footer>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" width="30%">
      <p class>
        <span></span>
        <el-select v-model="oldType[0]" disabled>
          <el-option
            v-for="item in oldType"
            :key="item.typeName"
            :label="item.typeName"
            :value="item.id"
          />
        </el-select>
      </p>
      <p>
        <span></span>
        <el-select v-model="newTypeId" placeholder="">
          <el-option
            v-for="item in typeList"
            :key="item.typeName"
            :label="item.typeName"
            :value="item.id"
          />
        </el-select>
      </p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="submitTrans"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import mixin from "@/util/mixin.js";
import Pagination from "../common/pagination";

export default {
  mixins: [mixin],
  components: {
    Pagination
  },
  data() {
    return {
      isSecond: false,
      dialogVisible: false,
      //
      oldType: [],
      originalId: "",
      typeList: [],
      newTypeId: ""
    };
  },
  created() {
    this.getTableData();
  },
  methods: {
    getTableData(page, id) {
      //typeList
      this.$axios("api/merchantGoodsType/merchant_goods_type_list")
        .then(res => {
          //console.log(res);
          this.typeList = res.data.data;
          //   //   ,parentlistchildrenlist
          //   this.tableData = this.list.filter((item)=>{
          //       return item.parentId === 0;
          //   })
        })
        .catch(err => {
          console.log(err);
        });
      // 
      this.$axios({
        method: "post",
        url: "api/merchantGoodsType/merchant_goods_type_list_page",
        data: {
          parentId: id || 0,
          currentPage: page || this.currentPage,
          pageSize: this.pageSize
        }
      })
        .then(res => {
          //console.log(res);
          this.tableData = res.data.data.list;
          this.totalPage = res.data.data.totalPage;
          this.dataCount = res.data.data.totalCount;
        })
        .catch(err => {
          console.log(err);
        });
    },
    toSecond(id) {
      this.isSecond = true;
      this.getTableData(1, id);
    },
    //
    addType(id) {
      this.$router.push({
        name: "add-type",
        query: { parentId: id }
      });
    },
    //
    gobackTable() {
      this.getTableData(1);
      this.isSecond = false;
    },
    //
    handleShowStatus($event, data) {
      let show = $event ? 1 : 0;
      this.$axios({
        method: "post",
        url: "api/merchantGoodsType/merchant_goods_type_update",
        data: {
          id: data.id,
          showStatus: show
        }
      })
        .then(res => {
          //console.log(res);
          this.$message({
            message: "",
            type: "success",
            showClose: true,
            duration: 2500
          });
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    transType(data) {
      this.dialogVisible = true;
      this.oldType = [];
      this.oldType.push(data);
    },
    submitTrans() {
      this.$axios({
        method: "post",
        url: "api/merchantGoodsType/merchant_goods_type_transfer",
        data: {
          originalId: this.oldType[0].id,
          lastId: this.newTypeId
        }
      })
        .then(res => {
          //console.log(res);
          this.msg();
        })
        .catch(err => {
          // console.log(err);
          this.msg(err, "error");
        });
      this.dialogVisible = false;
      this.isSecond = false;
      this.getTableData();
    },
        // (+)
    remove(id) {
      //id[]
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$axios({
          method: "post",
          url: "api/merchantGoodsType/merchant_goods_type_delete",
          data: {
            id: id
          }
        })
          .then(() => {
            this.msg("");
            this.getTableData();
          })
          .catch(err => {
            // console.log(err);
            this.msg(err, "error");
            this.getTableData();
          });
      });
    },
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/scss/mixin.scss";

.types {
  @include table();
  
  footer {
    justify-content: flex-end !important;
  }

  .el-dialog {
    p {
      height: 60px;
      &:first-child > span {
        margin-right: 14px;
      }
    }
    .el-select {
      margin-left: 20px;
    }
  }
}
</style>
