<template>
  <div class="refund-detail">
    <div class="main-wrapper">
      <div class="table-wrapper">
        <div class="header"></div>
        <table>
          <tr>
            <td></td>
            <td>{{tableData.code}}</td>
          </tr>
          <tr>
            <td></td>
            <td>{{returnStatus(tableData.status)}}</td>
          </tr>
          <tr>
            <td></td>
            <td>{{tableData.orderCode}}</td>
          </tr>
          <tr>
            <td></td>
            <td>{{tableData.creatTime}}</td>
          </tr>
          <tr>
            <td></td>
            <td>{{tableData.mobilePhone}}</td>
          </tr>
        </table>
        <table>
          <tr>
            <td></td>
            <td>{{tableData.applicationReturnMoney}}</td>
          </tr>
          <tr>
            <td></td>
            <td>
              <span v-if="tableData.status !== 1">{{tableData.returnMoney}}</span>
            </td>
          </tr>
          <tr>
            <td></td>
            <td></td>
          </tr>
          <tr>
            <td></td>
            <td>{{tableData.returnReason}}</td>
          </tr>
        </table>
        <table>
          <tr>
            <td></td>
            <td>
              <el-input
                v-model="resolveRemark"
                v-if="tableData.status == 1 || tableData.status == 2"
              />
              <span v-if="tableData.status !== 1">{{tableData.resolveRemark}}</span>
            </td>
          </tr>
        </table>
      </div>
      <div class="btn-group">
        <el-button v-if="tableData.status== 2" @click="agreeOrRejectReturn(4)"></el-button>
        <el-button type="danger" v-if="tableData.status== 2" @click="agreeOrRejectReturn(5)"></el-button>
        <el-button v-if="tableData.status !== 2" @click="$router.go(-1)"></el-button>
      </div>
    </div>
  </div>
</template>

<script>
import mixin from "@/util/mixin.js";

export default {
  mixins: [mixin], //vue 
  data() {
    return {
      resolveRemark: ""
    };
  },

  created() {
    this.getTableData();
  },
  computed: {
    //
    problemPics() {
      let picArr = [];
      if (this.tableData.problemPhoto) {
        if (this.tableData.problemPhoto.indexOf(",") !== -1) {
          picArr = this.this.tableData.problemPhoto.split(",");
        } else {
          picArr.push(this.tableData.problemPhoto);
        }
      }
      return picArr;
    }
  },
  methods: {
    //
    getTableData() {
      //
      this.$axios({
        method: "post",
        url: "api/merchant_return_goods/query_By_Id",
        data: {
          id: this.$route.query.orderId,
          merchantId: JSON.parse(localStorage.user).merchantId
        }
      })
        .then(res => {
          console.log(res);
          //
          this.tableData = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    returnStatus(status) {
      //0123 45 6 20
      switch (status) {
        case (1, 2):
          return "";
          break;
        default:
          return "";
      }
    },
    //
    agreeOrRejectReturn(status) {
      this.$axios({
        method: "post",
        url: "api/merchant_return_goods/update",
        data: {
          id: this.tableData.id,
          status: status,
          resolveRemark: this.resolveRemark
        }
      }).then(
        res => {
          this.$message({
            message: "",
            type: "success",
            showClose: true,
            duration: 2500
          });
          setTimeout(() => {
            this.$router.back();
          }, 3000);
        },
        err => {}
      );
    },
  }
};
</script>

