<template>
  <div class="refund-certificate">
    <!--  -->
    <div class="main-wrapper">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="200px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="remark1">
          <div>{{certiFile}}</div>
          <el-upload
            class="upload-demo"
            action="url"
            :http-request="uploadFile"
            accept="application/x-pkcs12"
          >
            <el-button type="primary">{{ruleForm.remark1 ? '' : ''}}</el-button>
            <div slot="tip" class="el-upload__tip">p12</div>
          </el-upload>
          <img src="../../assets/img/setting/wechat-merchant.png" alt class="form-img">
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitRuleForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import mixin from "@/util/mixin.js";

export default {
  mixins: [mixin],
  data() {
    return {
      fileList: [],
      ruleForm: {
        remark1: ""
      },
      rules: {
        remark1: [{ required: true, message: "", trigger: "change" }]
      }
    };
  },
  created() {
    this.getFormData();
  },
  computed: {
    certiFile() {
      return this.ruleForm.remark1.split("/").pop();
    }
  },
  methods: {
    //data
    getFormData() {
      this.$axios({
        method: "post",
        url: "api/merchant_pay_mode/query",
        data: {
          merchantId: JSON.parse(localStorage.user).merchantId
        }
      })
        .then(res => {
          // console.log(res);
          this.ruleForm = res.data.data;
        })
        .catch(err => {
          this.msg(err, "error");
        });
    },
    //
    uploadFile(content) {
      if (content.file.type !== "application/x-pkcs12") {
        this.msg("p12", "error");
        return;
      } else {
        let formData = new FormData(); //formdata
        formData.append("file", content.file); //formdata()\
        //
        this.$axios({
          method: "post",
          url: "api/file/file_upload",
          data: formData,
          type: "form",
          headers: {
            "Content-Type": "multipart/form-data" //* json
          }
        })
          .then(res => {
            this.msg("");
            // console.log(res);
            //res
          })
          .catch(err => {
            this.msg(err, "error");
          });
      }
    },
    //
    submitRuleForm(formName) {
      this.$refs[formName].validate(valid => {
        //
        if (valid) {
          // this.submitForm("api/?", this.ruleForm);
          this.msg("");
          this.getFormData();
        } else {
          this.msg("error submit!!", "error");
          return false;
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.refund-certificate {
  .main-wrapper {
    display: flex;
    justify-content: center;
    .el-form {
      img {
        width: 100%;
      }
    }
  }
}
</style>
